	const [enable, setEnable] = useState(true);
	const [loading, setLoading] = useState(false);
	const [modalBOC, setModalBOC] = useState({ open: false });
	const { productList } = useSelector((state) => state.choosenProducts);
	const { productListCompare } = useSelector(
		(state) => state.comparisonProducts
	);
	const haveChoosen = productList.find(
		(p) => p.stock.id === product.data.stock.id
	);
	const [collapsed, setCollapsed] = useState(false);
	const addToCartHandler = () => {
		addToCartCheck(product, quantity, token, setLoading, dispatch, cart);
	};
	const [selectedLocation, setSelectedLocation] = useState();
	const locations = product.locations;
	const max_count = parseInt(product.data.count);
	const min = parseInt(
		product.product.minOrder ? product.product.minOrder : 1
	);
	const { cartDescription } = useSelector(
		(state) => state.marketData.settings
	);

	const haveCompare = productListCompare.find(
		(p) => p?.stock?.id === product?.data?.stock?.id
	);

	function onChange(qty) {
		if (parseInt(qty) > max_count) {
			toast.error(t("amount-balance"), {
				position: "bottom-left",
			});
		} else {
			setQuantity(parseInt(qty));
		}
	}

	function openMapModal(e) {
		dispatch(openModal("map"));
		e.preventDefault();
	}
	function onComplatedMiddle(variationId) {
		if (variationId) {
			setEnable(true);
			onComplated(variationId);
		} else {
			setEnable(false);
		}
	}

	useEffect(() => {
		setQuantity(1);
	}, [product]);

