import React from "react";
import { SlideToggle } from "react-slide-toggle";
import CheckboxGroup from "./Checkboxes/CheckboxGroup";

function Filters({ filters }) {
	function changeIcon(e) {
		e.target.classList.toggle("collapsed");
	}

	function toTop() {
		window.scroll({
			top: 121,
		});
	}

	return filters[1]
		.filter(([idProp, dataProp]) => dataProp.choices.length !== 0)
		.map(([idProp, dataProp], index) => {
			const choices = Object.values(dataProp.choices);
			return (
				<SlideToggle collapsed={false} key={index}>
					{({ onToggle, setCollapsibleElement }) => (
						<div className="filter-top widget-collapsible">
							<div className="filter-item">
								<h3 className="widget-title">
									<a
										href="#Size"
										onClick={(e) => {
											onToggle(e);
											changeIcon(e);
											e.preventDefault();
											toTop();
										}}
										role="button"
										aria-expanded="true"
										aria-controls="widget-1"
									>
										{dataProp.label}
									</a>
								</h3>
								<div
									className="filter-body p-0"
									ref={setCollapsibleElement}
								>
									<div className="widget-body">
										<div className="filter-items">
											<CheckboxGroup
												name={`${filters[0]}[${idProp}]`}
												options={choices}
											/>
										</div>
										{/* )} */}
									</div>
								</div>
							</div>
						</div>
					)}
				</SlideToggle>
			);
		});
}

export default Filters;
