import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Modal from "react-modal";
import { useSelector } from "react-redux";
import { buyOneClick } from "../../../api/user";
import Loader from "../loaders/loader";
const customStyles = {
	content: {
		top: "50%",
		transform: "translateY(-50%)"
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10000"
	}
};
const styleModalBody = {
	padding: "2rem"
};

Modal.setAppElement("#root");
const startPhone = "+998 ";

export default function BuyOneClick({ setModal, modal }) {
	const { t } = useTranslation();
	const { shippingServices } = useSelector(state => state.marketData);
	const [shippingService, setShippingService] = useState();
	const location = useSelector(state => state.user.location);
	const [loading, setLoading] = useState(false);
	const [name, setName] = useState("");
	const [phone, setPhone] = useState(startPhone);
	const [error, setError] = useState("");
	const [success, setSuccess] = useState(false);

	let timer;
	const onSubmit = e => {
		e.preventDefault();
		let match = /^(\+998)?(\s)?\d{2}(\s)?\d{3}(\s)?\d{2}(\s)?\d{2}$/.test(
			phone
		);

		if (match && name.length > 2) {
			setLoading(true);
			console.log(location.address);
			buyOneClick(
				name,
				phone,
				modal.product.data.stock.id,
				modal.quantity,
				{
					...location.address,
					...location.coordinate
				},
				shippingService ? shippingService : shippingServices[0].id
			)
				.then(() => {
					setSuccess(true);
					setTimeout(() => {
						closeModal();
					}, 10000);
					setLoading(false);
				})
				.catch(err => {
					console.error(err);
					alert("error\n" + err.message);
					setLoading(false);
				});
		} else {
			setError("Iltimos tog'ri to'ldiring!");
		}
	};

	function closeModal() {
		document.getElementById("buyoneclick") &&
			document
				.getElementById("buyoneclick")
				.classList.remove("ReactModal__Content--after-open");

		timer = setTimeout(() => {
			setModal({ open: false });
			setLoading(false);
			setPhone(startPhone);
		}, 200);
	}

	useEffect(() => {
		return () => {
			if (timer) clearTimeout(timer);
		};
	});

	return (
		<Modal
			isOpen={modal.open}
			onRequestClose={closeModal}
			style={customStyles}
			contentLabel="Buy one click"
			className="modal-dialog"
			id="buyoneclick"
		>
			<div className="modal-content">
				<div className="modal-body" style={styleModalBody}>
					<button
						type="button"
						className="close"
						data-dismiss="modal"
						aria-label="Close"
						onClick={closeModal}
					>
						<span aria-hidden="true">
							<i className="icon-close"></i>
						</span>
					</button>
					<div>
						{success ? (
							<div>
								<p>{t("text-1")}</p>
								<p>{t("text-2")}</p>
							</div>
						) : loading ? (
							<Loader />
						) : (
							<form onSubmit={onSubmit}>
								<div className="form-group">
									<label htmlFor="name">
										{t("name-client")}
									</label>
									<input
										type="text"
										className="form-control"
										id="name"
										name="name"
										value={name}
										onChange={e => {
											setName(e.target.value);
											if (error) setError("");
										}}
										required
									/>
									<label htmlFor="phone">
										{t("phone-number")} *
									</label>
									<input
										type="text"
										className="form-control"
										id="phone"
										name="phone"
										value={phone}
										onChange={e => {
											setPhone(e.target.value);
											if (error) setError("");
										}}
										required
									/>
									<label htmlFor="shipping_service">
										Тип доставка
									</label>
									<select
										className="form-control"
										style={{
											borderRadius: "15px"
										}}
										onChange={e =>
											setShippingService(e.target.value)
										}
										name="shipping_serice"
										id="shipping_service"
									>
										{shippingServices.map(service => {
											return (
												<option
													key={service.id}
													value={service.id}
												>
													{service.name}
												</option>
											);
										})}
									</select>
									{error ? <span>{error}</span> : null}
									<button className="btn-product bg-primary text-white w-50 mx-auto p-4 mt-2">
										{t("buy")}
									</button>
								</div>
							</form>
						)}
					</div>
				</div>
			</div>
		</Modal>
	);
}
