import React from "react";
import { Link, useHistory } from "react-router-dom";
import { useState } from "react";
import MobileCategoryMenuRight from "./MobileCategoryMenuRight";
import { useSelector } from "react-redux";
import MediaImage from "../../../features/product/MediaImage";
import { dt } from "../../../../i18n";

function MobileCategoryMenu() {
	const h = useHistory();
	const { menuVertical } = useSelector(state => state.menu);
	const [choosenMenu, setChoosenMenu] = useState(menuVertical[0]);
	const [choosenLinks, setChoosenLinks] = useState(false);

	if (!choosenMenu) {
		return "";
	}
	function setting(menu) {
		setChoosenMenu(menu);
		setChoosenLinks(true);
	}
	function render(menu) {
		h.push(`/category/${menu.slug}`);
		document.querySelector("body").classList.remove("mmenu-active");
	}

	return (
		<nav className="mobile-nav d-flex" style={{ marginTop: "10px" }}>
			<div className="col-3 menu-side">
				<ul className="mobile-menu mobile-menu2 first-mobile-menu">
					{menuVertical.map(menu => (
						<li key={menu.id}>
							<div
								onClick={() => {
									menu.children.length > 0
										? setting(menu)
										: render(menu);
								}}
							>
								<div
									className={`mobile-menu-grid-item2 ${
										choosenMenu.id === menu.id
											? "active"
											: ""
									}`}
								>
									<div className="mobile-m-img2">
										{menu.image && (
											<MediaImage
												image={menu.image}
												largeSize={"50x_"}
												mediumSize={"50x_"}
												smallSize={"50x_"}
												alt={menu.name}
											/>
										)}
									</div>
									<div className="mobile-m-name2">
										{dt(menu.name)}
									</div>
									{menu.children.length > 0 ? (
										<div className="d-none circle">
											<Link
												to="#"
												style={{ zIndex: 2111515 }}
												onClick={() => render(menu)}
											>
												<svg
													width="16"
													height="16"
													viewBox="0 0 16 16"
													fill="none"
													xmlns="http://www.w3.org/2000/svg"
												>
													<path
														fillRule="evenodd"
														clipRule="evenodd"
														d="M14 8C14 11.3137 11.3137 14 8 14C4.68629 14 2 11.3137 2 8C2 4.68629 4.68629 2 8 2C11.3137 2 14 4.68629 14 8ZM16 8C16 12.4183 12.4183 16 8 16C3.58172 16 0 12.4183 0 8C0 3.58172 3.58172 0 8 0C12.4183 0 16 3.58172 16 8ZM8 10C9.10457 10 10 9.10457 10 8C10 6.89543 9.10457 6 8 6C6.89543 6 6 6.89543 6 8C6 9.10457 6.89543 10 8 10Z"
														fill="#CBCBD3"
													/>
												</svg>
											</Link>
											<i className="icon-arrow-right"></i>
										</div>
									) : null}
								</div>
							</div>
						</li>
					))}
				</ul>
			</div>
			<div
				className="col-9 categories-side"
				style={choosenLinks ? { zIndex: 1 } : { zIndex: 0 }}
			>
				<MobileCategoryMenuRight
					choosenMenu={choosenMenu}
					choosenLinks={choosenLinks}
					setChoosenLinks={setChoosenLinks}
					render={render}
				/>
			</div>
		</nav>
	);
}

export default MobileCategoryMenu;
