import React, { useState, useEffect } from "react";
import Filters from "./Filters";
import ShimmerProduct from "../../../../components/features/loaders/shimmer";
import { getFilterOfMenu } from "../../../../api/menu";
import { reduxForm } from "redux-form";
import { setFiltersData } from "../../../../actions/any";
import CategorySidebar from "./category-sidebar";
import { useTranslation } from "react-i18next";
import market from "../../../../config/market";

const emptyFilters = {
	variationProperties: [],
	productProperties: [],
	suppliers: []
};

function calculateChoosenPropsCount(props) {
	// window.localStorage.setItem("PrevFilters", JSON.stringify(props));
	// console.log(props);
	return (
		(props && Object.values(props).reduce((cv, pv) => cv + pv.length, 0)) ||
		0
	);
}

function FilterSideThree(props) {
	const hideSideBar = props.hideSideBar;
	const menu = props.menu;
	let count =
		(props.initialValues &&
			calculateChoosenPropsCount(props.initialValues.properties) +
				calculateChoosenPropsCount(
					props.initialValues.productProperties
				)) ||
		0;

	const [toggle, setToggle] = useState(false);
	const [loading, setLoading] = useState(true);
	const [filters, setFilters] = useState({});
	const { t } = useTranslation();
	useEffect(() => {
		window.addEventListener("resize", resizeHandle);
		resizeHandle();

		return () => {
			window.removeEventListener("resize", resizeHandle);
		};
	}, []);

	function resizeHandle() {
		if (window.document.querySelector("body")?.offsetWidth < 992)
			setToggle(true);
		else setToggle(false);
	}

	useEffect(() => {
		setLoading(true);

		getFilterOfMenu(props.menu.collection)
			.then(res => {
				props.dispatch(setFiltersData(res));
				setFilters(res);
				setLoading(false);
			})
			.catch(() => {
				setFilters(emptyFilters);
				setLoading(false);
			});
	}, [props.menu.collection]);

	if (loading) {
		return <ShimmerProduct />;
	}

	if (
		filters.variationProperties.length === 0 &&
		filters.productProperties.length === 0 &&
		filters.suppliers.length === 0
	) {
		return null;
	}
	const { handleSubmit } = props;

	return (
		<form onSubmit={handleSubmit}>
			<div className="filter-close-but">
				{market.config.home === 5 ? (
					<button
						// disabled={count < 1 ? true : false}
						className="btn-filter-close"
						onClick={() => hideSideBar()}
					>
						<span className="filter-closer">{t("close")}</span>
					</button>
				) : (
					<div
						className="btn-filter-close"
						onClick={() => hideSideBar()}
					>
						<span className="filter-closer">
							{t("close-filter")}
						</span>
					</div>
				)}
			</div>
			<aside
				className={toggle ? "sidebar-filter" : "sidebar sidebar-shop"}
			>
				<div className="filter-header d-none">
					<button className="closer" onClick={() => hideSideBar()}>
						<img src={market.config.icons.backArrow} alt="back" />
					</button>
					<p>{t("filters")}</p>
				</div>
				<div className={toggle ? "sidebar-filter-wrapper" : ""}>
					<div className="widget widget-clean">
						<label>
							{market.config.home === 5
								? `Выбрано: ${count}`
								: t("filter")}
						</label>
						<a
							href="##"
							className="sidebar-filter-clear"
							onClick={e => {
								e.preventDefault();
								props.clearAll();
							}}
						>
							{market.config.home === 5
								? t("reset")
								: t("clear-all")}
						</a>
					</div>

					{menu.children.length > 0 && (
						<CategorySidebar menu={menu} />
					)}

					{filters.variationProperties !== [] ? (
						<Filters
							filters={[
								"properties",
								Object.entries(filters.variationProperties)
							]}
						/>
					) : null}
					{filters.productProperties.length !== [] ? (
						<Filters
							filters={[
								"productProperties",
								Object.entries(filters.productProperties)
							]}
						/>
					) : null}
				</div>
			</aside>
			<div className="sidebar-filter-overlay" onClick={hideSideBar}></div>
		</form>
	);
}

export default reduxForm(
	{
		form: "mainfilterFormThree",
		onChange: (values, dispatch, props, previousValues) => {
			props.submit();
		},
		enableReinitialize: true
	},
	{ setFiltersData }
)(FilterSideThree);
