import React, { useState, useEffect } from "react";
import ReactImageMagnify from "react-image-magnify";
import { Magnifier } from "react-image-magnifiers";
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css";
import market from "../../../../config/market";
import BackButton from "../../../../components/common/BackButton";
import { useTranslation } from "react-i18next";
import { getImageUrl, trueFalseReturn } from "../../../../utils/oxMethods";
import SimpleShimmer from "../../../../components/features/loaders/SimpleShimmer";
import { unstable_batchedUpdates } from "react-dom";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { productGalerySlider } from "../../../../components/options";

let placeHolderImage = [
	{
		id: 1,
		urls: {
			"50x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			"100x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			"150x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			"300x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			"800x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			original: "https://telegra.ph/file/3a01e53662cea1a425220.png"
		}
	}
];
function MediaOne(props) {
	const { product, adClass = "product-gallery-vertical", loading } = props;
	const [currentImage, setcurrentImage] = useState(0);
	const [bigImage, setBigImage] = useState("");
	const [activeIndex, setactiveIndex] = useState(0);

	const { t } = useTranslation();
	const bigImages =
		product.data.variation.images.length === 0
			? placeHolderImage
			: product.data.variation.images;
	const [photoIndex, setPhotoIndex] = useState(0);
	const [isOpen, setStatus] = useState(false);

	useEffect(() => {
		unstable_batchedUpdates(() => {
			setBigImage(getImageUrl(bigImages[0], "original"));
			setcurrentImage(0);
			setactiveIndex(0);
		});
	}, [product.data?.variation?.id]);

	if (!product) {
		window.location = process.env.PUBLIC_URL + "pages/404";
	}

	function openLightBox() {
		let index = parseInt(
			document.querySelector(".product-main-image").getAttribute("index")
		);

		if (!index) {
			index = 0;
		}
		setStatus(true);
		setPhotoIndex(index);
	}

	function closeLightBox() {
		setStatus(false);
	}

	const setNextHandler = () => {
		setPhotoIndex(photoIndex => (photoIndex + 1) % bigImages.length);
	};

	const setPrevHandler = () => {
		setPhotoIndex(
			photoIndex => (photoIndex + bigImages.length - 1) % bigImages.length
		);
	};

	const handleSetCurrentImg = (data, index) => {
		setcurrentImage(index);
		setactiveIndex(index);
		setBigImage(getImageUrl(data, "original"));
	};

	function change(next) {
		if (next) {
			if (bigImages[currentImage + 1]) {
				setcurrentImage(currentImage + 1);
				setactiveIndex(activeIndex + 1);
			}
		} else {
			if (bigImages[currentImage - 1]) {
				setcurrentImage(currentImage - 1);
				setactiveIndex(activeIndex - 1);
			}
		}
	}

	return (
		<>
			<div className={`product-gallery ${adClass}`}>
				<div className="row m-0">
					{market.config.home === 6 || market.config.home === 10 ? (
						<div className="back-button-div only-mobile">
							<BackButton />
						</div>
					) : null}
					{trueFalseReturn(
						loading,
						<SimpleShimmer height={"590px"} />,
						market.config.home === 6 ||
							market.config.home === 7 ||
							market.config.home === 10 ||
							market.config.home === 1 ? (
							<Splide
								className="splide"
								options={productGalerySlider}
							>
								{bigImages.map((item, index) => (
									<SplideSlide key={index}>
										<div
											className="big-img-with-view-button"
											key={index}
										>
											<img
												src={getImageUrl(item, "800x_")}
												alt="product back"
											/>
										</div>
									</SplideSlide>
								))}
							</Splide>
						) : (
							<>
								<figure
									className="product-main-image"
									style={{ position: "relative" }}
									index="0"
								>
									{product.discount ? (
										<span className="product-label label-sale">
											{product.discount}%
										</span>
									) : (
										""
									)}

									{0 === product.stock ? (
										<span className="product-label label-out">
											{t("out-of-stock")}
										</span>
									) : (
										""
									)}

									{market.config.magnify ? (
										<>
											<div className="image">
												<div className="change d-none">
													<span
														onClick={() =>
															change(0)
														}
														className="left icon-angle-left"
													></span>
													<span
														onClick={() =>
															change(1)
														}
														className="right icon-angle-right"
													></span>
												</div>
												<img
													className="current-image"
													src={getImageUrl(
														bigImages[currentImage],
														"800x_"
													)}
													alt="current_product_image"
												/>
											</div>
											<div className="side-by-side">
												<ReactImageMagnify
													{...{
														smallImage: {
															alt:
																"current_image",
															isFluidWidth: true,
															src: getImageUrl(
																bigImages[
																	currentImage
																],
																"800x_"
															)
														},
														largeImage: {
															src: bigImage,
															width: 2060,
															height: 2648
														}
													}}
												/>
											</div>
										</>
									) : (
										<Magnifier
											imageSrc={getImageUrl(
												bigImages[currentImage],
												"800x_"
											)}
											imageAlt={bigImages[0].originalName}
											largeImageSrc={getImageUrl(
												bigImages[currentImage],
												"800x_"
											)}
											dragToMove={false}
											mouseActivation="hover"
											cursorStyleActive="crosshair"
											id="product-zoom"
										/>
									)}
									{market.config.magnify ? (
										<button
											className="text-primary bg-white"
											style={{
												position: "absolute",
												padding: "5px 10px",
												right: "20px",
												bottom: "20px",
												zIndex: "999",
												boxShadow: "0 0 5px #ccc"
											}}
											onClick={openLightBox}
										>
											<i className="icon-arrows"></i>
										</button>
									) : (
										<button
											style={{
												position: "absolute",
												width: "100%",
												top: "0",
												left: "0",
												right: "0",
												bottom: "0",
												zIndex: "9999"
											}}
											onClick={openLightBox}
										></button>
									)}
								</figure>
								{market.config.home === 6 ||
								market.config.home === 1 ||
								market.config.home === 10 ? null : (
									<div className="gallery-scrollable">
										<div className="gradient-div d-none" />
										<div
											id="product-zoom-gallery"
											className="product-image-gallery w-100"
										>
											{bigImages.map((item, index) => (
												<button
													className={`product-gallery-item ${
														activeIndex === index
															? "active"
															: ""
													}`}
													key={
														product.id + "-" + index
													}
													onClick={() =>
														handleSetCurrentImg(
															item,
															index
														)
													}
												>
													<img
														src={getImageUrl(
															item,
															"150x_"
														)}
														alt="product back"
														width="150"
														height="150"
													/>
												</button>
											))}
										</div>
									</div>
								)}
							</>
						)
					)}
				</div>
			</div>

			{isOpen ? (
				<Lightbox
					mainSrc={getImageUrl(bigImages[photoIndex], "original")}
					nextSrc={getImageUrl(
						bigImages[(photoIndex + 1) % bigImages.length],
						"original"
					)}
					prevSrc={getImageUrl(
						bigImages[(photoIndex - 1) % bigImages.length],
						"original"
					)}
					onCloseRequest={closeLightBox}
					onMovePrevRequest={setNextHandler}
					onMoveNextRequest={setPrevHandler}
				/>
			) : (
				""
			)}
		</>
	);
}

export default React.memo(MediaOne);
