import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import MediaImage from "../../../../../components/features/product/MediaImage";

export default function ProductSuppliers({ suppliers, product }) {
	const { t } = useTranslation();
	const [showAll, setShowAll] = useState(false);
	const [variation] = useState(
		product.variations.find((v) => v.id === product.data.variation.id)
	);
	const stocks = [];

	if (variation) {
		for (let i = 0; i < variation.stocks?.length; i++) {
			if (stocks.indexOf(variation.stocks[i].supplier) === -1) {
				stocks.push(variation.stocks[i]);
			}
		}
	}
	return (
		<div className="d-flex flex-column suppliers_ul">
			<div className={`suppliers_list ${showAll ? "active" : ""}`}>
				{suppliers?.map((supplier, i) => {
					return (
						<div
							onClick={() => {
								product.data.stock.id = stocks.find(
									(equel) => equel?.supplier === supplier?.id
								).id;
							}}
							key={i}
							className={`supplier-div ${
								stocks.find(
									(equel) => equel.supplier === supplier.id
								)
									? "enabled"
									: "disabled"
							} ${
								product.data.stock.supplier === supplier.id
									? "active "
									: ""
							}`}
						>
							<div className="seller-info-name d-none">
								<span style={{ marginBottom: "5px" }}>
									{t("supplier")}:&nbsp;
								</span>
								<p>{supplier.name}</p>
							</div>
							<Link
								to={`/supplier/${supplier.id}`}
								className={`supplier`}
								style={{
									display: "flex",
									alignItems: "center",
								}}
							>
								<div className="seller-info-img">
									{supplier.logo ? (
										<MediaImage
											image={supplier.logo}
											largeSize="300x_"
											mediumSize="150x_"
											smallSize="150x_"
											alt={supplier.name}
										/>
									) : null}
								</div>
								<div className="seller-info-name">
									<span style={{ marginBottom: "5px" }}>
										{t("supplier")}
									</span>
									<p>{supplier.name}</p>
								</div>
								<div className="suplier-product-price d-none">
									<span>{t("price")}</span>
									<p>{product?.data?.newPrice} UZS</p>
								</div>
							</Link>
						</div>
					);
				})}
			</div>
			{suppliers.length > 2 ? (
				<button
					className={`btn show-all-button ${showAll ? `d-none` : ``}`}
					onClick={() => setShowAll(!showAll)}
				>
					{t("view-all")}
				</button>
			) : null}
		</div>
	);
}
