import React from "react";
import GirdItemsMenu from "./GirdItemsMenu";
import { SlideToggle } from "react-slide-toggle";
import market from "../../../../config/market";
import { Link } from "react-router-dom";
import { dt } from "../../../../i18n";
import MediaImage from "../../../features/product/MediaImage";

function MobileCategoryMenuRight({ choosenMenu, setChoosenLinks, render }) {
	function changeIcon(e) {
		e.target.classList.toggle("collapsed");
	}

	if (choosenMenu.children.length === 0) {
		return <h3 className="text-center pt-5">Empty</h3>;
	}
	if (choosenMenu.children.length > 0) {
		return (
			<div>
				{market.config.home === 6 ||
				market.config.home === 8 ||
				market.config.home === 9 ||
				market.config.home === 1 ? (
					<div
						className="choosen-menu-name d-flex align-items-center"
						onClick={() => {
							setChoosenLinks(false);
						}}
					>
						<img src={market.config.icons.backArrow} alt="back" />
						<h3 className="mb-0">
							<b>Главное Меню</b>
						</h3>
					</div>
				) : null}

				{choosenMenu.children
					?.sort(function(a, b) {
						return a.sort - b.sort;
					})
					.map((menu, index) => (
						<SlideToggle
							collapsed={index === 0 ? false : true}
							key={menu.id}
						>
							{({ onToggle, setCollapsibleElement }) => (
								<div className="widget widget-collapsible">
									{market.config.home === 4 ||
									(market.config.home === 7 &&
										menu.children.length) ? (
										<>
											<Link
												to={`/category/${menu.slug}`}
												onClick={() => {
													let mobileMenu = document.querySelector(
														"body"
													);
													mobileMenu &&
														mobileMenu.classList.remove(
															"mmenu-active"
														);
												}}
												className="d-block widget-title"
											>
												<div className="menu-icon">
													<MediaImage
														image={menu.image}
														largeSize="800x_"
														mediumSize="800x_"
														smallSize="800x_"
														alt="image"
													/>
												</div>

												{dt(menu.name)}
											</Link>
										</>
									) : (
										<>
											<div className="d-none">
												<Link
													to={
														menu.children.length > 0
															? `#Size`
															: `${menu.slug}`
													}
													onClick={e =>
														menu.children.length > 0
															? (() => {
																	e.preventDefault();
																	onToggle(e);
																	changeIcon(
																		e
																	);
															  })()
															: render(menu)
													}
													role="button"
													aria-expanded="true"
													aria-controls="widget-1"
													className={
														index === 0
															? "collapsed"
															: ""
													}
												>
													<MediaImage
														image={menu.image}
														largeSize="800x_"
														mediumSize="800x_"
														smallSize="800x_"
														alt="image"
													/>
												</Link>
											</div>

											<h3 className="widget-title">
												<Link
													to={
														menu.children.length > 0
															? `#Size`
															: `${menu.slug}`
													}
													onClick={e =>
														menu.children.length > 0
															? (() => {
																	e.preventDefault();
																	onToggle(e);
																	changeIcon(
																		e
																	);
															  })()
															: render(menu)
													}
													role="button"
													aria-expanded="true"
													aria-controls="widget-1"
													className={
														index === 0
															? "collapsed"
															: ""
													}
												>
													{dt(menu.name)}
													<button
														className="d-none"
														style={{
															zIndex: 2111515
														}}
														onClick={() =>
															render(menu)
														}
													>
														<svg
															width="16"
															height="16"
															viewBox="0 0 16 16"
															fill="none"
															xmlns="http://www.w3.org/2000/svg"
														>
															<path
																fillRule="evenodd"
																d="M14 8C14 11.3137 11.3137 14 8 14C4.68629 14 2 11.3137 2 8C2 4.68629 4.68629 2 8 2C11.3137 2 14 4.68629 14 8ZM16 8C16 12.4183 12.4183 16 8 16C3.58172 16 0 12.4183 0 8C0 3.58172 3.58172 0 8 0C12.4183 0 16 3.58172 16 8ZM8 10C9.10457 10 10 9.10457 10 8C10 6.89543 9.10457 6 8 6C6.89543 6 6 6.89543 6 8C6 9.10457 6.89543 10 8 10Z"
																fill="#CBCBD3"
															/>
														</svg>
													</button>
												</Link>
											</h3>
										</>
									)}

									<div
										style={{ overflow: "hidden" }}
										ref={setCollapsibleElement}
									>
										<div className="widget-body">
											<GirdItemsMenu menu={menu} />
										</div>
									</div>
								</div>
							)}
						</SlideToggle>
					))}
			</div>
		);
	}

	return <GirdItemsMenu menu={choosenMenu} />;
}

export default MobileCategoryMenuRight;
