import React, { useEffect } from "react";
import { useState } from "react";
import { useSelector } from "react-redux";
import MediaImage from "../../../features/product/MediaImage";
import { dt } from "../../../../i18n";
import MobileCategoryMenuRightTwo from "./MobileCategoryMenuRightTwo";

function MobileCategoryMenuTwo({ isClicked, setIsClicked }) {
	const { menuVertical } = useSelector(state => state.menu);
	const [choosenMenu, setChoosenMenu] = useState();

	useEffect(() => {
		if (isClicked === false) {
			setChoosenMenu();
		}
	}, [isClicked]);

	function settings(menu) {
		if (menu.children.length > 0) {
			setChoosenMenu(menu);
			setIsClicked(true);
		}
	}

	return (
		<div className="menu-side">
			<ul className="mobile-menu mobile-menu2 first-mobile-menu row">
				{choosenMenu ? (
					<MobileCategoryMenuRightTwo
						menu={choosenMenu}
						setChoosenMenu={setChoosenMenu}
						setIsClicked={setIsClicked}
					/>
				) : (
					menuVertical.map(menu => (
						<li
							key={menu.id}
							className="col-4"
							onClick={() => {
								settings(menu);
							}}
						>
							<div className={`mobile-menu-grid-item2`}>
								<div className="mobile-m-img2">
									{menu.image && (
										<MediaImage
											image={menu.image}
											largeSize={"50x_"}
											mediumSize={"500x_"}
											smallSize={"500x_"}
											alt={menu.name}
										/>
									)}
								</div>
								<div className="mobile-m-name2">
									{dt(menu.name)}
								</div>
							</div>
						</li>
					))
				)}
			</ul>
		</div>
	);
}

export default MobileCategoryMenuTwo;
