import React, { useState, useEffect } from "react";
import Filters from "./Filters-top";
import ShimmerProduct from "../../../../components/features/loaders/shimmer-product";
import { getFilterOfMenu } from "../../../../api/menu";
import { reduxForm } from "redux-form";
import { setFiltersData } from "../../../../actions/any";
import CategorySidebar from "./category-sidebar";

import { useTranslation } from "react-i18next";

const emptyFilters = {
	properties: [],
	productProperties: [],
	suppliers: [],
};

function FilterTop(props) {
	const { t } = useTranslation();
	const menu = props.menu;
	const [toggle, setToggle] = useState(false);
	const [category, setCategory] = useState(false);
	const [loading, setLoading] = useState(true);
	const [filters, setFilters] = useState({});
	const [filterView, setFilterView] = useState(false);
	useEffect(() => {
		window.addEventListener("resize", resizeHandle);
		resizeHandle();

		return () => {
			window.removeEventListener("resize", resizeHandle);
		};
	}, []);

	function resizeHandle() {
		if (window.document.querySelector("body")?.offsetWidth < 992) setToggle(true);
		else setToggle(false);
	}

	function hideSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
	}

	function toggleSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
		else
			document
				.querySelector("body")
				.classList.add("sidebar-filter-active");
	}

	useEffect(() => {
		setLoading(true);
		getFilterOfMenu(props.menu.collection)
			.then((res) => {
				props.dispatch(setFiltersData(res));
				setFilters(res);
				setLoading(false);
			})
			.catch((err) => {
				setFilters({ ...emptyFilters });
				setLoading(false);
			});
	}, [props.menu.id]);

	if (loading) {
		return <ShimmerProduct />;
	}

	const { handleSubmit } = props;

	function closeFilter(e) {
		if (e === "filterdiv") setFilterView(false);
	}

	if (
		filters.variationProperties.length === 0 &&
		filters.productProperties.length === 0 &&
		filters.suppliers.length === 0
	) {
		return (
			<>
				<div className="d-flex align-items-center">
					{menu.children.length > 0 && (
						<div
							className={`${
								toggle ? "d-none" : "d-flex"
							} align-items-center p-0 mr-3 category-button`}
							style={{ cursor: "pointer" }}
							onClick={() => {
								setCategory(true);
								setFilterView(true);
							}}
						>
							<img
								src="./assets/images/osto-icons/category-icon.png"
								alt="category icon"
								width="18"
								height="18"
								className="mr-2"
							/>
							<span>{t("category")}</span>
						</div>
					)}
				</div>
				<div
					className={`${toggle ? "filterdiv-left" : "filterdiv"}`}
					style={{
						display: toggle
							? "block"
							: !filterView
							? "none"
							: "block",
						position: toggle ? "absolute" : "fixed",
						top: "0",
						left: "0",
						right: "0",
						bottom: "0",
						backgroundColor: "rgba(0,0,0,0.5)",
						zIndex: "999999",
					}}
					onClick={(e) => closeFilter(e.target.className)}
				>
					<form
						onSubmit={handleSubmit}
						style={{
							backgroundColor: "#fff",
							padding: toggle ? "0" : "20px 10px",
							borderTop: "1px solid #eee",
							height: toggle ? "0" : "100%",
							marginTop: "-1px",
							width: "310px",
							overflowY: "scroll",
						}}
					>
						<div className="d-flex align-items-center mb-5">
							{menu.children.length > 0 && (
								<div
									className={`${
										toggle ? "d-none" : "d-flex"
									} align-items-center p-0 mr-3`}
									style={{ cursor: "pointer" }}
									onClick={() => {
										category
											? setFilterView(false)
											: setCategory(true);
									}}
								>
									<img
										src="./assets/images/osto-icons/category-icon.png"
										alt="category icon"
										width="18"
										height="18"
										className="mr-2"
									/>
									<span>{t("category")}</span>
								</div>
							)}
						</div>
						<aside
							className={`${
								toggle
									? "sidebar-filter"
									: "sidebar sidebar-shop"
							}`}
						>
							<div
								className={
									toggle
										? "sidebar-filter-wrapper"
										: "d-flex mb-2"
								}
							>
								{category &&
									!toggle &&
									menu.children.length > 0 && (
										<CategorySidebar menu={menu} />
									)}
							</div>
						</aside>

						{toggle ? (
							<button
								className="sidebar-fixed-toggler"
								onClick={toggleSideBar}
							>
								<i className="i-icon icon-cogs"></i>
							</button>
						) : null}
						<div
							className="sidebar-filter-overlay"
							onClick={hideSideBar}
						></div>
					</form>
				</div>
			</>
		);
	}

	return (
		<>
			<div className="d-flex align-items-center mb-1">
				{menu.children.length > 0 && (
					<div
						className={`${
							toggle ? "d-none" : "d-flex"
						} align-items-center p-0 mr-3 category-button`}
						style={{ cursor: "pointer" }}
						onClick={() => {
							setCategory(true);
							setFilterView(true);
						}}
					>
						<img
							src="./assets/images/osto-icons/category-icon.png"
							alt="category icon"
							width="18"
							height="18"
							className="mr-2"
						/>
						<span>{t("category")}</span>
					</div>
				)}
				<div
					className={` ${
						toggle ? "d-none" : "d-flex"
					} align-items-center p-0 filter-button`}
					style={{ cursor: "pointer" }}
					onClick={() => {
						setCategory(false);
						setFilterView(true);
					}}
				>
					<img
						src="./assets/images/osto-icons/filter-icon.png"
						alt="filter icon"
						width="20"
						height="20"
						className="mr-2"
					/>
					<span>{t("filter")}</span>
				</div>
			</div>
			<div
				className={`${toggle ? "filterdiv-left" : "filterdiv"}`}
				style={{
					display: toggle ? "block" : !filterView ? "none" : "block",
					position: toggle ? "absolute" : "fixed",
					top: "0",
					left: "0",
					right: "0",
					bottom: "0",
					backgroundColor: "rgba(0,0,0,0.5)",
					zIndex: "999999",
				}}
				onClick={(e) => closeFilter(e.target.className)}
			>
				<form
					onSubmit={handleSubmit}
					style={{
						backgroundColor: "#fff",
						padding: toggle ? "0" : "20px 20px",
						borderTop: "1px solid #eee",
						height: toggle ? "0" : "100%",
						width: "300px",
						overflowY: "scroll",
					}}
				>
					<aside
						className={`${
							toggle ? "sidebar-filter" : "sidebar sidebar-shop"
						}`}
					>
						<div
							className={
								toggle
									? "sidebar-filter-wrapper"
									: "d-block mb-2"
							}
						>
							{category && !toggle ? (
								menu.children.length > 0 && (
									<CategorySidebar menu={menu} />
								)
							) : (
								<>
									{filters.variationProperties !== [] ? (
										<Filters
											filters={[
												"properties",
												Object.entries(
													filters.variationProperties
												),
											]}
										/>
									) : null}

									{filters.productProperties.length !== [] ? (
										<Filters
											filters={[
												"productProperties",
												Object.entries(
													filters.productProperties
												),
											]}
										/>
									) : null}
								</>
							)}
						</div>
					</aside>

					{/* {toggle ? (
						<button
							className="sidebar-fixed-toggler"
							onClick={toggleSideBar}
						>
							<i className="i-icon icon-cogs"></i>
						</button>
					) : null}
					<div
						className="sidebar-filter-overlay"
						onClick={hideSideBar}
					></div> */}
				</form>
			</div>
		</>
	);
}

export default reduxForm(
	{
		form: "filterForm",
		onChange: (values, dispatch, props, previousValues) => {
			props.submit();
		},
		enableReinitialize: true,
	},
	{ setFiltersData }
)(FilterTop);
