import { getProductsPromotions } from "../../../../api/products";

export async function getAndSetProductPromotions(variations, setState) {
	// console.log(variations);
	const ids = variations.map(variation => variation.variation.id);
	// console.log(ids);
	if (!ids.length) return;

	await getProductsPromotions(ids).then(promotions => {
		if (!promotions.length) return;

		variations.forEach(productData => {
			const promotion = promotions.find(
				promotion => promotion.variationId === productData.variation.id
			);

			if (!promotion) return;
			// console.log(promotion.variationId);
			// console.log(promotion.discount);
			// console.log(promotion.discountPercent);
			productData.discount = promotion.discount;
			productData.discountPercent = promotion.discountPercent;
			productData.newPrice = promotion.newPrice;
			productData.oldPrice = promotion.oldPrice;
		});
	});

	setState(prev => ({ ...prev }));
}
