import React, { useEffect } from "react";
import market from "../../config/market";
import CategoryListOne from "./products/CategoryListOne";
import CategoryListTwo from "./products/CategoryListTwo";
import CategoryListThree from "./products/CategoryListThree";
import CategoryListFour from "./products/CategoryListFour";
import CategoryListFive from "./products/CategoryListFive";
import CategoryListSix from "./products/CategoryListSix";
import { useDispatch } from "react-redux";
import { setCategoryPath } from "../../actions/any";

export default function ProductPage(props) {
	const dispatch = useDispatch();
	useEffect(() => {
		dispatch(setCategoryPath(props.match.params.slug));
	}, [props.match.params.slug, dispatch]);

	switch (market.config.productList) {
		case 1:
			return <CategoryListOne {...props} />;
		case 2:
			return <CategoryListTwo {...props} />;
		case 3:
			return <CategoryListThree {...props} />;
		case 4:
			return <CategoryListFour {...props} />;
		case 5:
			return <CategoryListFive {...props} />;
		case 6:
			return <CategoryListSix {...props} />;
		default:
			return <CategoryListOne {...props} />;
	}
}
