import React, { useRef, useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import { connect } from "react-redux";

// import Breadcrumb from "../../../components/common/breadcrumb";
import FilterTop from "../filterSidebar/filters/FilterTop";
import ProductListTwo from "../../../components/features/product/details/ProductListTwo";
import { deepEqual } from "../../../utils/array";
import { useHistory } from "react-router-dom";
import { getSingleCollection } from "../../../api/collections";

let timeOut = setTimeout(() => {}, 1000);
const KEEP_TIME = 1000;
const queryString = require("qs");
const initialQuery = { page: 1, size: 15 };

function CategoryListThree(props) {
	const menu = props.menu;
	const history = useHistory();
	const [collection, setCollection] = useState();

	const [query, setQuery] = useState(
		window.location.search.length > 0
			? queryString.parse(window.location.search.slice(1))
			: { ...initialQuery }
	);

	if (!menu) {
		history.push(process.env.PUBLIC_URL + "/404");
	}
	const myRef = useRef(null);
	const executeScroll = () =>
		myRef.current.scrollIntoView({ behavior: "smooth" });
	useEffect(() => {
		if (window.location.search.length === 0) {
			if (!deepEqual(query, initialQuery)) {
				setQuery(initialQuery);
			}
		}
		getSingleCollection(menu.collection).then(res => {
			setCollection(res);
		});
	}, [menu.id]);

	return (
		<>
			<Helmet>
				<title>{collection?.title}</title>
				<meta name="description" content={collection?.description} />
				<meta name="keywords" content={collection?.keyword} />
			</Helmet>

			<h1 className="d-none">
				{menu.name} | {props.title}
			</h1>

			<div className="container" ref={myRef}>
				<div className="page-content osto-category-page">
					<div className="pl-3 pr-3">
						<hr className="mt-0" />
						<div className="row">
							<div
								className="col-lg-12 order-lg-first"
								style={{ marginBottom: "15px" }}
							>
								<FilterTop
									onSubmit={values => {
										clearTimeout(timeOut);
										timeOut = setTimeout(() => {
											if (!deepEqual(query, values)) {
												setQuery(prevQuery => ({
													...prevQuery,
													...values,
													page: 1
												}));
											}
										}, KEEP_TIME);
									}}
									clearAll={() => {
										setQuery(initialQuery);
									}}
									menu={menu}
									adClass="sidebar sidebar-shop"
									executeScroll={executeScroll}
									initialValues={query}
								/>
							</div>

							<div className="col-lg-12 skeleton-body skel-shop-products loaded">
								<ProductListTwo
									query={query}
									setQuery={setQuery}
									menu={menu}
									column="5cols"
									title={menu.name}
									executeScroll={executeScroll}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

const mapStateToProps = (state, props) => {
	return {
		menu: state.menu.menuList.find(
			menu => menu.slug === props.match.params.slug
		),
		props: props,
		title: state.marketData.title
	};
};

export default React.memo(connect(mapStateToProps)(CategoryListThree));
