import React, { useState, useEffect, Fragment } from "react";
import Modal from "react-modal";
import { getVariation } from "../../../api/products";
import { clearModalData, closeModal } from "../../../actions/modal";
import { connect } from "react-redux";
import ProductPageSkleton from "../loaders/ProductPageSkleton";
import market from "../../../config/market";
import ProductDetailOne from "../../../views/product/partials/details/sticky-detail";
import MediaOne from "../../../views/product/partials/media/media-one";
import MediaStiky from "../../../views/product/partials/media/media-sticky";
import DescOne from "../../../views/product/partials/description/desc-sticky";

const customStyles = {
	content: {
		top: "50%",
		transform: "translateY(-50%)"
	},
	overlay: {
		backgroundColor: "rgba(51,51,51,0.6)",
		zIndex: "1041"
	}
};

Modal.setAppElement("#root");

function QuickView(props) {
	const { modalName, openModal, productId, dispatch } = props;
	const [product, setProduct] = useState(null);
	const [loading, setLoading] = useState(true);
	const [selectVar, setSelectVar] = useState(false);
	const [loadingProduct, setLoadingProduct] = useState(false);
	let timer;

	function closeModalComponent() {
		document
			.getElementById("product-quickview")
			.classList.remove("ReactModal__Content--after-open");

		timer = setTimeout(() => {
			setLoading(true);
			setProduct(null);
			setSelectVar(false);

			dispatch(closeModal());
		}, 200);
	}

	useEffect(() => {
		return () => {
			if (timer) clearTimeout(timer);
		};
	});

	useEffect(() => {
		if (productId && openModal) {
			setLoading(true);
			getVariation(productId)
				.then(dataProduct => {
					setProduct(dataProduct);
					setSelectVar(dataProduct.variations.length >= 1);
					setLoading(false);
				})
				.catch(err => {
					console.error(err);
				});
			dispatch(clearModalData());
		}
	}, [productId]);

	function onComplated(variationId) {
		if (variationId) {
			setLoadingProduct(true);
			getVariation(variationId)
				.then(dataProduct => {
					setProduct(dataProduct);
					setSelectVar(true);
					setLoadingProduct(false);
					setLoading(false);
				})
				.catch(err => {
					console.error(err);
				});
		}
	}

	return (
		<Modal
			isOpen={openModal && modalName === "qv"}
			contentLabel="QuickView"
			onRequestClose={closeModalComponent}
			style={customStyles}
			className="container quickView-container quick-view"
			id="product-quickview"
		>
			<div className="quickView-content horizontal">
				<div className="row scrolling-row">
					{loading ? (
						<ProductPageSkleton />
					) : (
						<Fragment>
							<div className="col-lg-6 col-12 p-0">
								{market.config.home === 3 ? (
									<MediaOne
										adClass={"product-gallery-gorizontal"}
										loading={loadingProduct}
										id={productId}
										product={product}
									/>
								) : (
									<MediaStiky
										adClass={"product-gallery-gorizontal"}
										loading={loadingProduct}
										id={productId}
										product={product}
									/>
								)}
							</div>

							<div className="col-lg-6 col-12 quickview-desc">
								<div className="entry-summary row">
									<div className="col-md-12">
										<div className="entry-summary1"></div>
									</div>

									<div className="col-md-12">
										<div className="entry-summary2"></div>
									</div>
								</div>
								<ProductDetailOne
									product={product}
									onComplated={onComplated}
									selectVar={selectVar}
									name={product.product.name}
									closeModalComponent={closeModalComponent}
								/>
								<DescOne product={product} />
							</div>
						</Fragment>
					)}
				</div>
			</div>

			<button
				title="Close (Esc)"
				type="button"
				className="mfp-close"
				onClick={closeModalComponent}
			>
				<span>×</span>
			</button>
		</Modal>
	);
}

const mapStateToProps = state => {
	return {
		modalName: state.modal.modal,
		openModal: state.modal.showModal,
		productId: state.modal.data && state.modal.data.productId
	};
};

export default connect(mapStateToProps, null)(QuickView);
