import React from "react";
import GirdItemMenu from "./GirdItemMenu";

function GirdItemsMenu({ menu }) {
	return (
		<ul className="mobile-menu mobile-menu2">
			{menu.children.map(submenu => (
				<GirdItemMenu menu={submenu} key={submenu.id} />
			))}
		</ul>
	);
}

export default GirdItemsMenu;
