import React, { useRef, useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import { connect } from "react-redux";
import Breadcrumb from "../../../components/common/breadcrumb";
import { deepEqual } from "../../../utils/array";
import { useHistory } from "react-router-dom";
import { dt } from "../../../i18n";
import ProductListFive from "../../../components/features/product/details/ProductListFive";
import { getSingleCollection } from "../../../api/collections";
import FilterSideFive from "../filterSidebar/filters/FilterSideFive";
import CategorySideBarTwo from "../filterSidebar/filters/category-sidebar-two";
import market from "../../../config/market";
import Toolbox from "../../../components/features/product/details/toolbox-five";
import { useTranslation } from "react-i18next";

let timeOut = setTimeout(() => {}, 1000);
const KEEP_TIME = 1000;
const queryString = require("qs");
const initialQuery = { page: 1, size: 12 };
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true
};

function CategoryList(props) {
	const menu = props.menu;
	const history = useHistory();
	const [collection, setCollection] = useState();
	const [showText, setShowText] = useState(false);
	const pages = initialPages;
	const { t } = useTranslation();
	const [query, setQuery] = useState(
		window.location.search.length > 0
			? queryString.parse(window.location.search.slice(1))
			: { ...initialQuery }
	);

	if (!menu) {
		history.push(process.env.PUBLIC_URL + "/404");
	}
	const myRef = useRef(null);
	const executeScroll = () =>
		myRef.current.scrollIntoView({ behavior: "smooth" });

	function toggleSideBar() {
		if (
			window.document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			window.document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
		else
			window.document
				.querySelector("body")
				.classList.add("sidebar-filter-active");
	}

	useEffect(() => {
		if (window.location.search.length === 0) {
			if (!deepEqual(query, initialQuery)) {
				setQuery(initialQuery);
			}
		}
		getSingleCollection(menu.collection).then(res => {
			setCollection(res);
		});
	}, [menu?.id]);

	return (
		<>
			<Helmet>
				<title>{collection?.title}</title>
				<meta name="description" content={collection?.description} />
				<meta name="keywords" content={collection?.keyword} />
			</Helmet>

			<h1 className="d-none">
				{dt(menu.name)} | {dt(props.title)}
			</h1>

			<div className="category-six" ref={myRef}>
				<Breadcrumb
					breadcrumb={props.menus.map(menu => ({
						link: menu.slug,
						name: menu.name
					}))}
				/>
				<div className="page-content">
					<div className="container">
						<div className="row">
							<div className="col-lg-9 skeleton-body skel-shop-products loaded">
								<div className="row banners d-none">
									<div className="col-12 col-md-6 ">
										<img
											src="/assets/images/backgrounds/bg-img.svg"
											alt="banner"
										/>
									</div>
									<div className="col-12 col-md-6">
										<img
											src="/assets/images/backgrounds/bg-img.svg"
											alt="banner"
										/>
									</div>
								</div>
								<div className="menu-name">
									<h4>{menu.name}</h4>
									{menu.children.length > 0 ? (
										<CategorySideBarTwo menu={menu} />
									) : null}
									<div className="filterr">
										<button
											className="filter toogler"
											onClick={toggleSideBar}
										>
											<img
												src={market.config.icons.filter}
												alt="filter"
											/>
											<span className="ml-2">Фильтр</span>
										</button>
										<Toolbox
											setQuery={setQuery}
											pages={pages}
										/>
									</div>
								</div>
								<ProductListFive
									query={query}
									setQuery={setQuery}
									menu={menu}
									column="4cols"
									title={menu.name}
									executeScroll={executeScroll}
								/>
								{collection ? (
									<div className="category-description">
										<div
											className={`before-footer-text ${
												showText ? "active" : ""
											}`}
										>
											<p className="footer-desc mt-5">
												{collection.footerTitle}
											</p>
											<div
												className="footer-title text-dark"
												dangerouslySetInnerHTML={{
													__html:
														collection.footerDesc
												}}
											/>
										</div>
										<button
											className="opener"
											onClick={() =>
												setShowText(!showText)
											}
										>
											{showText ? t("hide") : t("more")}
										</button>
									</div>
								) : null}
							</div>

							<div className="col-lg-3 order-lg-first skeleton-body skel-shop-sidebar loaded filter-side">
								<FilterSideFive
									onSubmit={values => {
										clearTimeout(timeOut);
										timeOut = setTimeout(() => {
											if (!deepEqual(query, values)) {
												setQuery(prevQuery => ({
													...prevQuery,
													...values,
													page: 1
												}));
											}
										}, KEEP_TIME);
									}}
									clearAll={() => {
										setQuery(initialQuery);
									}}
									menus={props.menus}
									menu={menu}
									adClass="sidebar sidebar-shop"
									executeScroll={executeScroll}
									initialValues={query}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

const mapStateToProps = (state, props) => {
	const menu = state.menu.menuList.find(
		menu => menu.slug === props.match.params.slug
	);
	const menus = [menu];
	const menuList = state.menu.menuList;
	if (menus.length > 0) {
		for (let i = 0; ; i++) {
			if (menus[i]?.parent) {
				let parentMenu = menuList.find(
					menu => menu.id === menus[i].parent
				);
				menus.push(parentMenu);
			} else {
				break;
			}
		}
	}
	return {
		menu: state.menu.menuList.find(
			menu => menu.slug === props.match.params.slug
		),
		menus: menus.reverse(),
		props: props,
		title: state.marketData.title
	};
};

export default React.memo(connect(mapStateToProps)(CategoryList));
