import React from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { getBrandAttribute } from "../../../../../utils/oxMethods";

export default function ProductBrands({ product }) {
	const brands = product.brandAttribute;
	const { settings } = useSelector((state) => state.marketData);
	const { t } = useTranslation();
	const brandName = getBrandAttribute(product, settings);
	return (
		<div className="product-brand">
			<Link
				to={`/trademark/${brands?.property_value}`}
				className="supplier brands"
				style={{ display: "flex", alignItems: "center" }}
			>
				<div className="seller-info-name brand-name d-none">
					<span style={{ marginBottom: "5px" }}>
						{t("brand")}:&nbsp;
					</span>
					<p>{brandName}</p>
				</div>
				{brands?.image && (
					<div className="seller-info-img brand-img">
						<img
							src={brands.image.urls["100x_"]}
							alt={brands.property_value}
						/>
					</div>
				)}
				<div className="seller-info-name brand-name">
					<span style={{ marginBottom: "5px" }}>{t("brand")}</span>
					<p>{brands?.property_value}</p>
				</div>
			</Link>
		</div>
	);
}
