import React, { useState } from "react";
import SearchForm from "../../partials/SearchFormTwo";
import market from "../../../../config/market";
import { useTranslation } from "react-i18next";
import SearchModal from "../../../features/modal/SearchModal";
import MobileCategoryMenuTwo from "./MobileCategoryMenuTwo";

export default function MobileMenuTwo() {
	const { t } = useTranslation();
	const [isClicked, setIsClicked] = useState(false);
	const [search, setSearch] = useState(false);

	return (
		<div className="mobile-menu-container bg-dark">
			<div className="mobile-menu-header">
				{isClicked ? (
					<button
						className="back-button"
						onClick={() => setIsClicked(!isClicked)}
					>
						<img src={market.config.icons.backArrow} alt="back" />
					</button>
				) : null}
				<p> {t("catalog")}</p>
			</div>
			<div
				className="mobile-menu-search"
				onClick={() => setSearch(!search)}
			>
				<SearchForm />
			</div>
			<MobileCategoryMenuTwo
				isClicked={isClicked}
				setIsClicked={setIsClicked}
			/>
			<SearchModal search={search} setSearch={setSearch} />
		</div>
	);
}
