import React from "react";
import market from "../../../../../config/market";
import ProductBrands from "./ProductBrands";
import ProductSuppliers from "./ProductSuppliers";

export default function ProductBrandAttrAndSupplier({
	product,
	closeModalComponent,
}) {
	return (
		<div
			className={`col-md-12 col-12 product-cat d-flex ${
				product.suppliers.length > 0 ? `more-supplier` : ``
			}`}
		>
			<div className="product-cat-suppliers ">
				{market.config.supplierAtribute ? (
					<ProductSuppliers
						suppliers={product.suppliers}
						product={product}
						closeModalComponent={closeModalComponent}
					/>
				) : null}

				{product.brandAttribute && <ProductBrands product={product} />}
			</div>
		</div>
	);
}
