import React from "react";
import ShimmerProduct from "./shimmer-product";

function SimpleShimmerProducts({ size = 4 }) {
	const mockItems = Array(size).fill(1);

	return (
		<div className="row align-items-center w-100">
			{mockItems.map((_, index) => {
				return (
					<div
						key={index}
						className="col-lg-3 col-md-4 col-6 shimmer-products"
					>
						<ShimmerProduct />
					</div>
				);
			})}
		</div>
	);
}

export default SimpleShimmerProducts;
