import React, { useState, useEffect, Fragment } from "react";
import { setStatePages } from "../../../../actions/any";
import ProductCard from "..";
import QuickView from "../QuickViewNew";
import { addToCart } from "../../../../actions/cart";
import {
	getProductsPage,
	getProductsPromotions,
} from "../../../../api/products";
import FilterCanceler from "./FilterCanceler";
import ToolboxThree from "../../product/details/ToolboxThree";
import SimpleShimmerProducts from "../../loaders/SimpleShimmerProducts";
import ChooseVariationModal from "../../modal/choose-variation";
import { useTranslation } from "react-i18next";
import ProductCardRowTwo from "../ProductCardRowTwo";
import Pagination from "./Pagination";
import { getImageUrl } from "../../../../utils/oxMethods";
import { useDispatch } from "react-redux";
import market from "../../../../config/market";

const grid = {
	"2cols": "col-6",
	"3cols": "col-6 col-md-4 col-lg-4",
	"4cols": "col-6 col-md-4 col-lg-4 col-xl-3",
	"5cols": "col-6 col-md-4 col-lg-3 col-xl-5col",
	"12cols": "col-12",
};
const units = {
	"2cols": 6,
	"3cols": 9,
	"4cols": 12,
	"5cols": 15,
};
const queryString = require("qs");
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true,
	allPages: 1,
};

function ProductListFive({ menu, title, query, setQuery, column }) {
	const { t } = useTranslation();
	const [cols, setCols] = useState(column);
	const [loading, setLoading] = useState(true);
	const [pages, setPages] = useState(initialPages);
	const dispatch = useDispatch();

	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } },
	});

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct,
		});
	}

	useEffect(() => {
		let mount = true;
		setLoading(true);
		const strQuery = queryString.stringify(query);
		getProductsPage(menu.id, strQuery)
			.then((page) => {
				if (mount) {
					window.history.replaceState(
						null,
						title,
						`/category/${menu.slug}?` + strQuery
					);
					setPages((prevPage) => ({
						page: prevPage.page,
						totalCount: page.totalCount,
						variations: page.variations,
						hasMore: page.variations.length > 0,
						allPages: Math.ceil(page.totalCount / query.size),
					}));
					setLoading(false);

					const ids = page.variations.reduce((pv, nv) => {
						return [...pv, nv.variation.id];
					}, []);

					if (ids.length) {
						getProductsPromotions(ids)
							.then((res) => {
								if (res.length) {
									const products = page.variations.map(
										(variation) => {
											return {
												...variation,
												...res.find(
													(v) =>
														v.variationId ===
														variation.variation.id
												),
											};
										}
									);
									setPages((prevPage) => ({
										page: prevPage.page,
										totalCount: page.totalCount,
										variations: products,
										hasMore: page.variations.length > 0,
										allPages: Math.ceil(
											page.totalCount / query.size
										),
									}));
								}
							})
							.catch((err) => {
								console.log(err);
							});
					}
				}
			})
			.catch((err) => {
				if (mount) {
					setPages((prevPage) => ({
						...prevPage,
						hasMore: false,
					}));
					setLoading(false);
				}
			});
		return () => {
			mount = false;
		};
	}, [query, menu.id]);

	useEffect(() => {
		if (pages.variations.length > 0) {
			let p = [];
			pages.variations.map((page) =>
				p.push({
					id: page.variation.id,
					name: page.variation.name,
					image: getImageUrl(page.variation.images[0], "300x_"),
				})
			);
			dispatch(setStatePages(p));
		}
	}, [pages.variations]);

	return (
		<>
			{pages.variations?.length > 0 ? (
				<div className="product-toolbox">
					<span
						className="filter d-none"
						onClick={() =>
							window.document
								.querySelector("body")
								.classList.add("sidebar-filter-active")
						}
					>
						<span>{t("filter")}</span>
						<img src={market.config.icons.filter} alt="filter" />
					</span>
					<ToolboxThree
						setCols={setCols}
						setQuery={setQuery}
						cols={cols}
						pages={pages}
					/>
				</div>
			) : null}
			<FilterCanceler query={query} setQuery={setQuery} />
			{loading ? (
				<SimpleShimmerProducts columns={units[column] / 4} />
			) : (
				<div className="products mb-3">
					{pages.variations.length === 0 ? (
						<div className="row justify-content-center">
							<h4>{t("nothing-found")}</h4>
						</div>
					) : (
						<Fragment>
							<div className="row">
								{pages.variations.map((product) => (
									<div
										className={`${grid[cols]} d-flex`}
										key={product.variation.id}
									>
										{cols === "12cols" ? (
											<ProductCardRowTwo
												product={product}
												type="sidebar"
												onAddToCart={(dataProduct) => {
													onAddToCart(dataProduct);
												}}
											/>
										) : (
											<ProductCard
												product={product}
												type="sidebar"
												onAddToCart={(dataProduct) => {
													onAddToCart(dataProduct);
												}}
											/>
										)}
									</div>
								))}
							</div>
							<QuickView />
							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
								addToCart={addToCart}
							/>
						</Fragment>
					)}
				</div>
			)}
			{pages.allPages > 1 ? (
				<div className="page-pagination">
					<Pagination
						pageCount={pages.allPages}
						onChange={({ selected }) => {
							setQuery((prev) => ({
								...prev,
								page: 1 + selected,
							}));
						}}
					/>
				</div>
			) : null}
		</>
	);
}

export default ProductListFive;
