import React, { useState } from "react";
import { Link } from "react-router-dom";
import market from "../../../../config/market";
import { LinkComponent } from "../Desktop/LinkComponent";

function MobileCategoryMenuRightTwo({ menu, setIsClicked, setChoosenMenu }) {
	if (!menu) {
		return "";
	}
	const [collapse, setCollapse] = useState(0);

	// useEffect(() => {
	// 	window.addEventListener("popstate", e => {
	// 		console.log(e);

	// 	});
	// }, []);
	console.log(menu);

	return (
		<div className="first-menu-children">
			{menu.children.map((children, i) =>
				i !== menu.children.length - 1 ? (
					<div key={i} className="widget">
						<p
							className={`d-flex align-items-center justify-content-between widget-title ${
								collapse !== i + 1 ? "" : "active"
							}`}
							onClick={() =>
								setCollapse(collapse === i + 1 ? 0 : i + 1)
							}
						>
							<span
								style={{
									maxWidth: "80%",
								}}
							>
								{children.name}
							</span>
							<span className="d-flex align-items-center">
								<Link
									to={`/category/${children.slug}`}
									onClick={() => {
										let mobileMenu = document.querySelector(
											"body"
										);
										setIsClicked(false);
										mobileMenu &&
											mobileMenu.classList.remove(
												"mmenu-active"
											);
									}}
									className="mr-3 pl-3 pr-3"
								>
									<img
										style={{
											minWidth: "16px",
											minHeight: "16px",
										}}
										src={market.config.icons.circle}
										alt="category"
									/>
								</Link>
								<img
									src={market.config.icons.rArrow}
									alt="collapse"
								/>
							</span>
						</p>

						<ul
							className={`widget-list ${
								collapse !== i + 1 ? "" : "active"
							}`}
						>
							{children.children &&
								children.children.map((child, i) => (
									<li key={i}>
										<LinkComponent menu={child} />
									</li>
								))}
						</ul>
					</div>
				) : (
					<div key={i} className="widget">
						<LinkComponent menu={children} />
					</div>
				)
			)}
		</div>
	);
}

export default MobileCategoryMenuRightTwo;
