import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import { dt } from "../../../../i18n";

const Menu = (path, menu) => {
	const [menuChild, setMenuChild] = useState(false);
	const [subMenuChild, setSubMenuChild] = useState(false);

	return (
		<li key={menu.id}>
			<Link
				to={menu.children.length > 0 ? "#" : `/category/${menu.slug}`}
				onClick={() => {
					return menu.children.length > 0
						? setMenuChild(!menuChild)
						: document
								.querySelector("body")
								.classList.remove("mmenu-active");
				}}
			>
				{dt(menu.name)}
				<span
					style={{
						transform: menuChild ? "rotate(180deg)" : ""
					}}
					className={menu.children.length > 0 ? "mmenu-btn" : "null"}
				></span>
			</Link>

			<ul
				className="sf-with-ul p-0"
				style={{
					display: "block",
					height: menuChild ? "auto" : "0",
					transition: "0.3s",
					overflow: "hidden"
				}}
			>
				{menu.children.map(subMenu => (
					<li key={subMenu.id} className="sf-with-ul">
						<Link
							to={
								subMenu.children.length > 0
									? "#"
									: `/category/${menu.slug}`
							}
							onClick={() => {
								return subMenu.children.length > 0
									? setSubMenuChild(!subMenuChild)
									: document
											.querySelector("body")
											.classList.remove("mmenu-active");
							}}
						>
							{dt(subMenu.name)}
							<span
								style={{
									transform: subMenuChild
										? "rotate(180deg)"
										: ""
								}}
								className={
									subMenu.children.length > 0
										? "mmenu-btn"
										: "null"
								}
							></span>
						</Link>

						<ul
							className="sf-with-ul p-0"
							style={{
								display: "block",
								transition: "0.3s",
								height: subMenuChild ? "auto" : "0",
								overflow: "hidden"
							}}
						>
							{subMenu.children.map(subSubMenu => (
								<li key={subSubMenu.id}>
									<Link
										onClick={() => {
											document
												.querySelector("body")
												.classList.remove(
													"mmenu-active"
												);
										}}
										to={`/category/${subSubMenu.slug}`}
									>
										{dt(subSubMenu.name)}
									</Link>
								</li>
							))}
						</ul>
					</li>
				))}
			</ul>
		</li>
	);
};

function MainMobileMenu(props) {
	const allMenuData = props.menu;
	const [path, setPath] = useState("");

	useEffect(() => {
		setPath(window.location.href);
	});

	return (
		<nav className="mobile-nav">
			<ul className="mobile-menu">
				{allMenuData.menuHorizontal.map(menu => Menu(path, menu))}
			</ul>
		</nav>
	);
}

function mapStateToProps(state) {
	return {
		menu: state.menu
	};
}

export default React.memo(connect(mapStateToProps)(MainMobileMenu));
