import React, { useState } from "react";
// import { connect } from 'react-redux';
import { Magnifier } from "react-image-magnifiers";
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css";
import { useTranslation } from "react-i18next";
import { getImageUrl, trueFalseReturn } from "../../../../utils/oxMethods";
import SimpleShimmer from "../../../../components/features/loaders/SimpleShimmer";

let placeHolderImage = [
	{
		id: 1,
		urls: {
			"50x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			"100x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			"150x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			"300x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			"800x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
			original: "https://telegra.ph/file/3a01e53662cea1a425220.png"
		}
	}
];

function MediaOne(props) {
	const { product, adClass = "product-gallery-separated" } = props;

	const { t } = useTranslation();

	const [photoIndex, setPhotoIndex] = useState(0);
	const [isOpen, setStatus] = useState(false);

	const bigImages =
		product.data.variation.images.length === 0
			? placeHolderImage
			: product.data.variation.images;

	function openLightBox() {
		let index = parseInt(
			document.querySelector(".product-main-image").getAttribute("index")
		);

		if (!index) {
			index = 0;
		}
		setStatus(true);
		setPhotoIndex(index);
	}

	function closeLightBox() {
		setStatus(false);
	}

	const setNextHandler = () => {
		setPhotoIndex(photoIndex => (photoIndex + 1) % bigImages.length);
	};

	const setPrevHandler = () => {
		setPhotoIndex(
			photoIndex => (photoIndex + bigImages.length - 1) % bigImages.length
		);
	};

	return (
		<>
			<div className={`product-gallery ${adClass}`}>
				<div className="row m-0">
					<figure className="product-main-image w-100" index="0">
						{product.discount ? (
							<span className="product-label label-sale">
								{product.discount}%
							</span>
						) : (
							""
						)}

						{0 === product.stock ? (
							<span className="product-label label-out">
								{t("out-of-stock")}
							</span>
						) : (
							""
						)}

						{trueFalseReturn(
							props.loading,
							<div className="media-sticky mt-2">
								<SimpleShimmer height={"800px"} />
							</div>,
							bigImages.map((item, i) => (
								<div key={i} className="media-sticky mt-2">
									<Magnifier
										key={i}
										imageSrc={getImageUrl(item, "800x_")}
										imageAlt={product.data.variation.name}
										largeImageSrc={getImageUrl(
											item,
											"800x_"
										)}
										dragToMove={false}
										mouseActivation="hover"
										cursorStyleActive="crosshair"
										id="product-zoom"
									/>
								</div>
							))
						)}

						<button
							id="btn-product-gallery"
							className="btn-product-gallery"
							onClick={openLightBox}
						>
							<i className="icon-arrows"></i>
						</button>
					</figure>
				</div>
			</div>

			{isOpen ? (
				<Lightbox
					mainSrc={getImageUrl(bigImages[photoIndex], "original")}
					nextSrc={getImageUrl(
						bigImages[(photoIndex + 1) % bigImages.length],
						"original"
					)}
					prevSrc={getImageUrl(
						bigImages[
							(photoIndex + bigImages.length - 1) %
								bigImages.length
						],
						"original"
					)}
					onCloseRequest={closeLightBox}
					onMovePrevRequest={setNextHandler}
					onMoveNextRequest={setPrevHandler}
				/>
			) : (
				""
			)}
		</>
	);
}

export default MediaOne;
