import React from "react";
import { Link } from "react-router-dom";
import { dt } from "../../../../i18n";
import MediaImage from "../../../features/product/MediaImage";

function GirdItemMenu({ menu }) {
	return (
		<li className="col-6 m-0 p-0 mobile-menu-grid-link">
			<Link
				to={`/category/${menu.slug}`}
				onClick={() => {
					let mobileMenu = document.querySelector("body");
					mobileMenu && mobileMenu.classList.remove("mmenu-active");
				}}
			>
				<div className="mobile-menu-grid-item-wrapper">
					<div className="mobile-menu-grid-item">
						{menu.image && (
							<MediaImage
								largeSize="150x_"
								mediumSize="150x_"
								smallSize="150x_"
								image={menu.image}
								alt={menu.image + menu.id}
							/>
						)}
					</div>

					<div className="mobile-m-name">{dt(menu.name)}</div>
				</div>
			</Link>
		</li>
	);
}

export default GirdItemMenu;
