import React, { useState, useEffect, Fragment } from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import { setStatePages } from "../../../../actions/any";
import ProductCard from "..";
import { addToCart } from "../../../../actions/cart";
import {
	getProductsPage,
	getProductsPromotions
} from "../../../../api/products";
import Toolbox from "./toolbox-two";
import FilterCanceler from "./FilterCanceler";
import SimpleShimmerProducts from "../../loaders/SimpleShimmerProducts";
import ChooseVariationModal from "../../modal/choose-variation";
import LoadMoreButton from "./loadMoreButton";
import { useTranslation } from "react-i18next";
import { getImageUrl } from "../../../../utils/oxMethods";
import { useDispatch } from "react-redux";
const MORE_LOAD_PAGE_STEP = 3;
const grid = {
	"2cols": "col-6",
	"3cols": "col-6 col-md-4 col-lg-4",
	"4cols": "col-6 col-md-4 col-lg-4 col-xl-3",
	"5cols": "col-6 col-md-4 col-lg-3 col-xl-5col p-0"
};
const units = { "2cols": 6, "3cols": 9, "4cols": 12, "5cols": 15 };
const queryString = require("qs");
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true
};

function ProductListTwo({ menu, title, query, setQuery, column }) {
	const dispatch = useDispatch();
	const [cols] = useState(column);
	const [loading, setLoading] = useState(true);
	const [pages, setPages] = useState(initialPages);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } }
	});
	let mount = true;

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct
		});
	}
	const { t } = useTranslation();

	async function getProducts(query, more) {
		const strQuery = queryString.stringify(query);
		getProductsPage(menu.id, strQuery)
			.then(async page => {
				window.history.replaceState(
					null,
					title,
					`/category/${menu.slug}?` + strQuery
				);
				if (mount) {
					setPages(prevPage => ({
						page: page.page,
						totalCount: page.totalCount,
						variations: more
							? [...prevPage.variations, ...page.variations]
							: page.variations,

						hasMore: page.variations.length > 0
					}));
					setLoading(false);

					const ids = page.variations.reduce((pv, nv) => {
						return [...pv, nv.variation.id];
					}, []);

					let products = more ? pages.variations : [];
					if (ids.length) {
						await getProductsPromotions(ids).then(res => {
							if (res.length) {
								page.variations.forEach(variation => {
									products.push({
										...variation,
										...res.find(
											v =>
												v.variationId ===
												variation.variation.id
										)
									});
								});
							}
						});
						setPages(prevPage => ({
							...prevPage,
							variations: products
						}));
					}
				}
			})
			.catch(err => {
				setPages(prevPage => ({
					...prevPage,
					hasMore: false
				}));
				setLoading(false);
			});
	}

	function fetchMoreData(load_more = false) {
		if (pages.totalCount === pages.variations.length) {
			setPages(prevPage => ({
				...prevPage,
				more_load_time: false,
				hasMore: false
			}));
			return;
		}
		if (!load_more && pages.page % MORE_LOAD_PAGE_STEP === 0) {
			setPages(prevPage => ({
				...prevPage,
				hasMore: false,
				more_load_time: true
			}));
			return;
		}
		query.page = parseInt(query.page) + 1;
		getProducts(query, true);
	}

	useEffect(() => {
		setLoading(true);
		getProducts(query, false);
		return () => {
			mount = false;
		};
	}, [query, menu.id]);

	useEffect(() => {
		if (pages.variations.length > 0) {
			let p = [];
			pages.variations.map(page =>
				p.push({
					id: page.variation.id,
					name: page.variation.name,
					image: getImageUrl(page.variation.images[0], "300x_")
				})
			);
			dispatch(setStatePages(p));
		}
	}, [pages.variations]);

	return (
		<>
			<div className="d-flex bd-highlight">
				<h2 className="flex-grow-1 bd-highlight category-title">
					{title}
				</h2>
				<Toolbox setQuery={setQuery} pages={pages} />
			</div>

			<FilterCanceler query={query} setQuery={setQuery} />
			{loading ? (
				<SimpleShimmerProducts columns={units[column] / 4} />
			) : (
				<div className="products mb-3">
					{pages.variations.length === 0 ? (
						<div className="row">
							<h4>{t("nothing-found")}</h4>
						</div>
					) : (
						<Fragment>
							<InfiniteScroll
								dataLength={pages.variations.length}
								next={fetchMoreData}
								hasMore={pages.hasMore}
								loader={
									<div className="w-100">
										<SimpleShimmerProducts />
									</div>
								}
								className="row"
							>
								{pages.variations.map(product => (
									<div
										className={`${grid[cols]} d-flex`}
										key={product.variation.id}
									>
										<ProductCard
											product={product}
											type="sidebar"
											onAddToCart={dataProduct => {
												onAddToCart(dataProduct);
											}}
										/>
									</div>
								))}
							</InfiniteScroll>
							<LoadMoreButton
								pages={pages}
								fetchMoreData={fetchMoreData}
							/>
							{/* <QuickView /> */}
							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
								addToCart={addToCart}
							/>
						</Fragment>
					)}
				</div>
			)}
		</>
	);
}

export default ProductListTwo;
