import React, { useState, useEffect } from "react";
import Filters from "./Filters";
import ShimmerProduct from "../../../../components/features/loaders/shimmer";
import { getFilterOfMenu } from "../../../../api/menu";
import { Field, reduxForm } from "redux-form";
import { setFiltersData } from "../../../../actions/any";
import CategorySidebarTwo from "./category-sidebar-two";
import { useTranslation } from "react-i18next";
import "rc-slider/assets/index.css";
import market from "../../../../config/market";

const emptyFilters = {
	variationProperties: [],
	productProperties: [],
	suppliers: []
};

function counter({ input, className, placeholder }) {
	return (
		<input
			{...input}
			type={"number"}
			className={className}
			placeholder={placeholder}
			onChange={e => {
				input.onChange(e.target.value);
			}}
		/>
	);
}

function FilterSideSix(props) {
	const menu = props.menu;
	const [toggle, setToggle] = useState(false);
	const [loading, setLoading] = useState(true);
	const [filters, setFilters] = useState({});
	const { t } = useTranslation();

	useEffect(() => {
		window.addEventListener("resize", resizeHandle);
		resizeHandle();

		return () => {
			window.removeEventListener("resize", resizeHandle);
		};
	}, []);

	function resizeHandle() {
		if (window.document.querySelector("body")?.offsetWidth < 992)
			setToggle(true);
		else setToggle(false);
	}

	function hideSideBar() {
		if (
			window.document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			window.document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
	}

	useEffect(() => {
		setLoading(true);
		let mount = true;
		getFilterOfMenu(props.menu?.collection)
			.then(res => {
				if (mount) {
					props.dispatch(setFiltersData(res));
					setFilters(res);
					setLoading(false);
				}
			})
			.catch(err => {
				if (mount) {
					setFilters({ ...emptyFilters });
					setLoading(false);
				}
			});
		return () => {
			mount = false;
		};
	}, [props.menu?.collection]);

	if (loading) {
		return <ShimmerProduct />;
	}
	const { handleSubmit } = props;

	return (
		<form onSubmit={handleSubmit}>
			<aside
				className={toggle ? "sidebar-filter" : "sidebar sidebar-shop"}
			>
				<div className="menu-name">
					<h4>{menu?.name}</h4>
					{toggle ? (
						<button className="closer" onClick={hideSideBar}>
							<img
								src={market.config.icons.closer}
								alt="closer"
							/>
						</button>
					) : null}
				</div>
				{menu?.children?.length > 0 ? (
					<CategorySidebarTwo menu={menu} />
				) : null}
				<div className={toggle ? "sidebar-filter-wrapper w-100" : ""}>
					<div className="filter-price">
						<div className="clear-btn">
							<h4>{t("Цена в UZS")}</h4>
							{/* <button
								className="btn"
								onClick={() => counter(true)}
							>
								Сбросить
							</button> */}
						</div>
						<div className="d-flex justify-content-between">
							<Field
								name="sellPrice[min]"
								type="number"
								className="form-control mb-0 mr-2"
								placeholder={t("from-two")}
								component={counter}
							/>
							<Field
								name="sellPrice[max]"
								type="number"
								className="form-control mb-0"
								placeholder={t("to")}
								component={counter}
							/>
						</div>
					</div>

					<div className="filter-properties">
						{filters.variationProperties !== [] ? (
							<Filters
								filters={[
									"properties",
									Object.entries(filters.variationProperties)
								]}
							/>
						) : null}
						{filters.productProperties.length !== [] ? (
							<Filters
								filters={[
									"productProperties",
									Object.entries(filters.productProperties)
								]}
							/>
						) : null}
					</div>
				</div>
			</aside>

			<div className="sidebar-filter-overlay" onClick={hideSideBar} />
		</form>
	);
}

export default reduxForm(
	{
		form: "mainfilterForm",
		onChange: (values, dispatch, props, previousValues) => {
			props.submit();
		},
		enableReinitialize: true
	},
	{ setFiltersData }
)(FilterSideSix);
