import React, { useState, useEffect } from "react";
import Filters from "./Filters";
import ShimmerProduct from "../../../../components/features/loaders/shimmer";
import { getFilterOfMenu } from "../../../../api/menu";
import { Field, reduxForm } from "redux-form";
import { setFiltersData } from "../../../../actions/any";
import CategorySidebar from "./category-sidebar";
import { useTranslation } from "react-i18next";
import Slider from "rc-slider";
import "rc-slider/assets/index.css";
import market from "../../../../config/market";
import { formatterCurrency } from "../../../../utils/string";

const emptyFilters = {
	variationProperties: [],
	productProperties: [],
	suppliers: [],
};

function field({ input, price, marks, setPrice, index, className }) {
	return (
		<Slider
			className={className}
			defaultValue={price[index]}
			min={index === "min" ? 0 : 1000}
			max={index === "min" ? 450000 : 1000000}
			marks={marks}
			onChange={(e) => {
				input.onChange(e);
				setPrice({ ...price, [index]: e });
			}}
		/>
	);
}
function counter({ input, className, placeholder }) {
	return (
		<input
			{...input}
			className={className}
			placeholder={placeholder}
			onChange={(e) => {
				input.onChange(e.target.value);
			}}
		/>
	);
}

function FilterSide(props) {
	const menu = props.menu;

	const [toggle, setToggle] = useState(false);
	const [loading, setLoading] = useState(true);
	const [filters, setFilters] = useState({});
	const { t } = useTranslation();
	const [price, setPrice] = useState({ min: 0, max: 900000 });

	const styleMark = {
		whiteSpace: "nowrap",
		color: "black",
		fontWeight: "bold",
	};
	const minMark = {
		[price.min]: {
			style: styleMark,
			label: formatterCurrency(price.min),
		},
	};
	const maxMark = {
		[price.max]: {
			style: styleMark,
			label: formatterCurrency(price.max),
		},
	};

	useEffect(() => {
		window.addEventListener("resize", resizeHandle);
		resizeHandle();

		return () => {
			window.removeEventListener("resize", resizeHandle);
		};
	}, []);

	function resizeHandle() {
		if (window.document.querySelector("body")?.offsetWidth < 992)
			setToggle(true);
		else setToggle(false);
	}

	function hideSideBar() {
		if (
			window.document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			window.document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
	}

	function toggleSideBar() {
		if (
			window.document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			window.document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
		else
			window.document
				.querySelector("body")
				.classList.add("sidebar-filter-active");
	}

	useEffect(() => {
		setLoading(true);
		let mount = true;
		getFilterOfMenu(props.menu.collection)
			.then((res) => {
				if (mount) {
					props.dispatch(setFiltersData(res));
					setFilters(res);
					setLoading(false);
				}
			})
			.catch((err) => {
				if (mount) {
					setFilters({ ...emptyFilters });
					setLoading(false);
				}
			});
		return () => {
			mount = false;
		};
	}, [props.menu.collection]);

	if (loading) {
		return <ShimmerProduct />;
	}

	if (
		filters.variationProperties.length === 0 &&
		filters.productProperties.length === 0 &&
		filters.suppliers.length === 0
	) {
		return null;
	}
	const { handleSubmit } = props;

	return (
		<form onSubmit={handleSubmit}>
			<div className="filter-close-but">
				<div className="btn-filter-close" onClick={() => hideSideBar()}>
					<i className="icon-close"></i>
					<span>{t("close-filter")}</span>
				</div>
			</div>
			<a
				href="##"
				className="sidebar-filter-clear d-none"
				onClick={(e) => {
					e.preventDefault();
					props.clearAll();
				}}
			>
				{t("clear-all")}
			</a>
			<aside
				className={toggle ? "sidebar-filter" : "sidebar sidebar-shop"}
			>
				<div className={toggle ? "sidebar-filter-wrapper w-100" : ""}>
					<div className="widget widget-clean">
						<label>{t("filter")}:</label>
						<a
							href="##"
							className="sidebar-filter-clear"
							onClick={(e) => {
								e.preventDefault();
								props.clearAll();
							}}
						>
							{t("clear-all")}
						</a>
						<div className="filter-close-but d-none">
							<div
								className="btn-filter-close"
								onClick={() => hideSideBar()}
							>
								<i className="icon-close"></i>
								<span>{t("close-filter")}</span>
							</div>
						</div>
					</div>
					<div
						className="filter-price"
						style={
							market.config.home !== 6
								? {
										margin: "30px 0",
										padding: "0 30px",
								  }
								: null
						}
					>
						<h4>{t("price")}</h4>
						{market.config.home === 6 ||
						market.config.home === 1 ||
						market.config.home === 9 ||
						market.config.home === 8 ? (
							<div className="d-flex justify-content-between">
								<Field
									name="sellPrice[min]"
									type="number"
									className="form-control mr-4"
									placeholder={t("from-two")}
									component={counter}
								/>
								<Field
									name="sellPrice[max]"
									type="number"
									className="form-control"
									placeholder={t("to")}
									component={counter}
								/>
							</div>
						) : (
							<>
								<div>
									<span>{t("from-two")}</span>
									<Field
										name="sellPrice[min]"
										id="sellPrice[min]"
										component={field}
										className="min-slide mb-3"
										price={price}
										index={"min"}
										setPrice={setPrice}
										marks={minMark}
									/>
								</div>
								<div>
									<span>{t("to")}</span>
									<Field
										name="sellPrice[max]"
										id="sellPrice[max]"
										component={field}
										price={price}
										index={"max"}
										setPrice={setPrice}
										marks={maxMark}
									/>
								</div>
							</>
						)}
					</div>
					{menu.children.length > 0 && (
						<CategorySidebar menu={menu} />
					)}
					{filters.variationProperties !== [] ? (
						<Filters
							filters={[
								"properties",
								Object.entries(filters.variationProperties),
							]}
						/>
					) : null}
					{filters.productProperties.length !== [] ? (
						<Filters
							filters={[
								"productProperties",
								Object.entries(filters.productProperties),
							]}
						/>
					) : null}
				</div>
			</aside>
			{toggle ? (
				<button
					className="sidebar-fixed-toggler"
					onClick={toggleSideBar}
				>
					<span className="d-none">{t("filter")}</span>
					<img src={market.config.icons.filter} alt="filter" />
				</button>
			) : null}
			<div className="sidebar-filter-overlay" onClick={hideSideBar} />
		</form>
	);
}

export default reduxForm(
	{
		form: "mainfilterForm",
		onChange: (values, dispatch, props, previousValues) => {
			props.submit();
		},
		enableReinitialize: true,
	},
	{ setFiltersData }
)(FilterSide);
