import React, { useRef, useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import { connect } from "react-redux";
import FilterSideThree from "../filterSidebar/filters/FilterSideThree";
import ProductListFour from "../../../components/features/product/details/ProductListFour";
import { deepEqual } from "../../../utils/array";
import { useHistory } from "react-router-dom";
import { getSingleCollection } from "../../../api/collections";
import Breadcrumb from "../../../components/common/breadcrumb";

let timeOut = setTimeout(() => {}, 1000);
const KEEP_TIME = 1000;
const queryString = require("qs");
const initialQuery = { page: 1, size: 12 };

function CategoryListFour(props) {
	const menu = props.menu;
	const [collection, setCollection] = useState();
	const history = useHistory();

	const [query, setQuery] = useState(
		window.location.search.length > 0
			? queryString.parse(window.location.search.slice(1))
			: { ...initialQuery }
	);

	if (!menu) {
		history.push(process.env.PUBLIC_URL + "/404");
	}
	const myRef = useRef(null);
	const executeScroll = () =>
		myRef.current.scrollIntoView({ behavior: "smooth" });
	useEffect(() => {
		if (window.location.search.length === 0) {
			if (!deepEqual(query, initialQuery)) {
				setQuery(initialQuery);
			}
		}
		getSingleCollection(menu.collection).then(res => {
			setCollection(res);
		});
	}, [menu?.id]);

	function hideSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
	}

	function toggleSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
		else
			document
				.querySelector("body")
				.classList.add("sidebar-filter-active");
	}

	return (
		<>
			<Helmet>
				<title>{collection?.title}</title>
				<meta name="description" content={collection?.description} />
				<meta name="keywords" content={collection?.keyword} />
			</Helmet>

			<h1 className="d-none">
				{menu.name} | {props.title}
			</h1>

			<div className="main product_list_three" ref={myRef}>
				<div className="page-content">
					<div className="container">
						<Breadcrumb
							breadcrumb={props.menus.map(menu => ({
								link: menu.slug,
								name: menu.name
							}))}
						/>

						<div className="row">
							<div className="col-lg-9 skeleton-body skel-shop-products loaded">
								<ProductListFour
									toggleSideBar={toggleSideBar}
									query={query}
									setQuery={setQuery}
									menu={menu}
									column="3cols"
									title={menu.name}
									executeScroll={executeScroll}
								/>
							</div>

							<div className="col-lg-3 order-lg-first skeleton-body skel-shop-sidebar loaded">
								<FilterSideThree
									hideSideBar={hideSideBar}
									onSubmit={values => {
										clearTimeout(timeOut);
										timeOut = setTimeout(() => {
											if (!deepEqual(query, values)) {
												setQuery(prevQuery => ({
													...prevQuery,
													...values,
													page: 1
												}));
											}
										}, KEEP_TIME);
									}}
									clearAll={() => {
										setQuery(initialQuery);
									}}
									menu={menu}
									adClass="sidebar sidebar-shop"
									executeScroll={executeScroll}
									initialValues={query}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

const mapStateToProps = (state, props) => {
	const menu = state.menu.menuList.find(
		menu => menu.slug === props.match.params.slug
	);
	const menus = [menu];
	const menuList = state.menu.menuList;
	if (menus.length > 0) {
		for (let i = 0; ; i++) {
			if (menus[i]?.parent) {
				let parentMenu = menuList.find(
					menu => menu.id === menus[i].parent
				);
				menus.push(parentMenu);
			} else {
				break;
			}
		}
	}
	return {
		menu: state.menu.menuList.find(
			menu => menu.slug === props.match.params.slug
		),
		menus: menus.reverse(),
		props: props,
		title: state.marketData.title
	};
};

export default React.memo(connect(mapStateToProps)(CategoryListFour));
