import mainCaller from "./mainCaller";
import store from "../store";

export function getCollectionProducts(collectionId, size = 6, query) {
	return mainCaller(
		`/${collectionId}/collection-products?${
			query ? query : `size=${size}&page=1`
		}`,
		"GET"
	);
}

export function getSingleCollection(id) {
	return mainCaller(`/collections/${id}`, "GET");
}
export function getLatestProducts(size) {
	const blockLatestId = store.getState().marketData.blockLatest;
	if (!blockLatestId) return Promise.resolve(null);
	return getCollectionProducts(blockLatestId, size);
}
export function getRecommendedProducts() {
	const blockRecommendedId = store.getState().marketData.blockRecommended;
	if (!blockRecommendedId) return Promise.resolve(null);
	return getCollectionProducts(blockRecommendedId);
}

export function getSpecialProducts() {
	const blockSpecialId = store.getState().marketData.blockSpecial;
	if (!blockSpecialId) return Promise.resolve(null);
	return getCollectionProducts(blockSpecialId);
}

export function getTopSellingProducts() {
	const blockTopSellingId = store.getState().marketData.blockTopSelling;
	if (!blockTopSellingId) return Promise.resolve(null);
	return getCollectionProducts(blockTopSellingId);
}
