import React, { useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import { Tabs, TabList, Tab, TabPanel } from "react-tabs";
import MobileNav from "./MainMobileMenu";
import CategoryMenu from "./MobileCategoryMenu";
import SearchForm from "../../partials/SearchForm";
import market from "../../../../config/market";
import BackButton from "../../../common/BackButton";

export default function MobileMenuOne({ home }) {
	const history = useHistory();

	useEffect(() => {
		const body = document.querySelector("body");
		if (history.location.pathname === "/category") {
			body.classList.add("category-active");
		}

		window.addEventListener("popstate", e => {
			if (body.classList.contains("category-active")) {
				body.classList.remove("category-active");
			}
		});
		return () => {
			body.classList.remove("category-active");
		};
	}, [history.location]);

	return (
		<div
			className={` mobile-menu-container bg-dark ${
				home === 2 ? "osto-mobile-menu-container" : ""
			}`}
		>
			<div className="mobile-menu-wrapper">
				<BackButton icon={market.config.icons.closer} />
				<div className="p-3 for-home-six">
					<SearchForm inputId="searchInputMobile" />
				</div>

				<Tabs defaultIndex={0} selectedTabClassName="show">
					<TabList
						className="nav nav-pills-mobile nav-border-anim"
						role="tablist"
					>
						<Tab className="text-center nav-item">
							<span className="nav-link">Каталог товаров</span>
						</Tab>

						<Tab className="text-center nav-item">
							<span className="nav-link">Меню</span>
						</Tab>
					</TabList>

					<div className="tab-content">
						<TabPanel>
							<CategoryMenu />
						</TabPanel>

						<TabPanel>
							<MobileNav />
						</TabPanel>
					</div>
				</Tabs>

				<div className="social-icons">
					<Link
						to="#"
						className="social-icon"
						target="_blank"
						title="Facebook"
					>
						<i className="icon-facebook-f"></i>
					</Link>
					<Link
						to="#"
						className="social-icon"
						target="_blank"
						title="Twitter"
					>
						<i className="icon-twitter"></i>
					</Link>
					<Link
						to="#"
						className="social-icon"
						target="_blank"
						title="Instagram"
					>
						<i className="icon-instagram"></i>
					</Link>
					<Link
						to="#"
						className="social-icon"
						target="_blank"
						title="Youtube"
					>
						<i className="icon-youtube"></i>
					</Link>
				</div>
			</div>
		</div>
	);
}
