import React, { useState, useEffect, Fragment } from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import { setStatePages } from "../../../../actions/any";
import ProductCard from "..";
import QuickView from "../QuickViewNew";
import { addToCart } from "../../../../actions/cart";
import {
	getProductsPage,
	getProductsPromotions
} from "../../../../api/products";
import ToolboxTwo from "./toolbox-two";
import FilterCanceler from "./FilterCanceler";
import SimpleShimmerProducts from "../../loaders/SimpleShimmerProducts";
import ChooseVariationModal from "../../modal/choose-variation";
import LoadMoreButton from "./loadMoreButton";
import { useTranslation } from "react-i18next";
import { getImageUrl } from "../../../../utils/oxMethods";
import BackButton from "../../../common/BackButton";
import SearchFormTwo from "../../../common/partials/SearchFormTwo";
import { dt } from "../../../../i18n";
import market from "../../../../config/market";
import SearchModal from "../../modal/SearchModal";
import { useDispatch, useSelector } from "react-redux";
import Pagination from "./Pagination";

const MORE_LOAD_PAGE_STEP = 5;
const grid = {
	"2cols": "col-6",
	"3cols": "col-6 col-md-4 col-lg-4",
	"4cols": "col-6 col-md-4 col-lg-4 col-xl-3",
	"5cols": "col-6 col-md-4 col-lg-3 col-xl-5col"
};
const units = { "2cols": 6, "3cols": 9, "4cols": 12, "5cols": 15 };
const queryString = require("qs");
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true,
	allPages: 1
};

function ProductListFour({
	menu,
	title,
	query,
	setQuery,
	column,
	toggleSideBar
}) {
	const dispatch = useDispatch();
	// const [fixedFilter, setFixedFilter] = useState({});
	const [isInputClicked, setIsInputClicked] = useState(false);
	const { t } = useTranslation();
	const [cols, setCols] = useState(column);

	const menuList = useSelector(state => state.menu.menuList);
	const parentMenu = menuList.find(menus => menus.id === menu.parent);
	const [loading, setLoading] = useState(true);
	const [pages, setPages] = useState(initialPages);
	const [search, setSearch] = useState(false);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } }
	});
	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct
		});
	}

	function toTop() {
		window.scroll({
			top: 0
		});
	}

	let mount = true;

	async function getProducts(query, more) {
		const strQuery = queryString.stringify(query);
		getProductsPage(menu.id, strQuery)
			.then(async page => {
				window.history.replaceState(
					null,
					title,
					`/category/${menu.slug}?` + strQuery
				);
				if (mount) {
					setPages(prevPage => ({
						page: page.page,
						totalCount: page.totalCount,
						variations: more
							? [...prevPage.variations, ...page.variations]
							: page.variations,

						hasMore: page.variations.length > 0,
						allPages: Math.ceil(page.totalCount / query.size)
					}));
					setLoading(false);

					const ids = page.variations.reduce((pv, nv) => {
						return [...pv, nv.variation.id];
					}, []);

					let products = more ? pages.variations : [];
					if (ids.length) {
						await getProductsPromotions(ids).then(res => {
							if (res.length) {
								page.variations.forEach(variation => {
									products.push({
										...variation,
										...res.find(
											v =>
												v.variationId ===
												variation.variation.id
										)
									});
								});
							}
						});
						setPages(prevPage => ({
							...prevPage,
							variations: products
						}));
					}
				}
			})
			.catch(err => {
				setPages(prevPage => ({
					...prevPage,
					hasMore: false
				}));
				setLoading(false);
			});
	}

	function fetchMoreData(load_more = false) {
		if (pages.totalCount === pages.variations.length) {
			setPages(prevPage => ({
				...prevPage,
				more_load_time: false,
				hasMore: false
			}));
			return;
		}
		if (!load_more && pages.page % MORE_LOAD_PAGE_STEP === 0) {
			setPages(prevPage => ({
				...prevPage,
				hasMore: false,
				more_load_time: true
			}));
			return;
		}

		if (query.page < pages.allPages) {
			query.page = parseInt(query.page) + 1;
			getProducts(query, true);
		} else
			setPages(prevPage => ({
				...prevPage,
				hasMore: false,
				more_load_time: true
			}));
	}

	useEffect(() => {
		setLoading(true);
		if (market.config.home === 9) {
			document
				.querySelector(".header-middle")
				.classList.add("only-desktop");
		}
		getProducts(query, false);
		toTop();
		return () => {
			mount = false;
		};
	}, [query, menu.id]);

	useEffect(
		() => {
			// let id = () => {
			// 	if (window.scrollY > 150) {
			// 		setFixedFilter({
			// 			position: "fixed",
			// 			top: "0",
			// 			left: "0",
			// 			right: "0",
			// 			zIndex: "9999"
			// 		});
			// 	} else {
			// 		setFixedFilter({
			// 			position: "relative"
			// 		});
			// 	}
			// };
			// window.addEventListener("scroll", id);
			if (window.innerWidth < 1000) {
				const header = document.querySelector(".header-six");
				header.setAttribute("style", "display: none");
			}
			return () => {
				// window.removeEventListener("scroll", id);
				// if (window.innerWidth < 500) {
				const header = document.querySelector(".header-six");
				header.setAttribute("style", "");
				// }
			};
		},
		[
			// window.scrollY
		]
	);

	useEffect(() => {
		if (pages.variations.length > 0) {
			let p = [];
			pages.variations.map(page =>
				p.push({
					id: page.variation.id,
					name: page.variation.name,
					image: getImageUrl(page.variation.images[0], "300x_")
				})
			);
			dispatch(setStatePages(p));
		}
	}, [pages.variations]);

	return (
		<div className="position-relative">
			{market.config.home === 5 ? (
				<div className="category-header only-mobile">
					<div className={"d-flex align-items-center"}>
						<BackButton
							icon={market.config.icons.backArrow}
							parentMenu={parentMenu}
						/>
					</div>
					<h2 className="category-title only-mobile">{dt(title)}</h2>
					<button
						className="btn-search"
						onClick={() => setSearch(!search)}
					>
						<img src={market.config.icons.search} alt="search" />
					</button>
				</div>
			) : (
				<div className="category-header">
					<div
						className={
							isInputClicked
								? "d-none"
								: "d-flex align-items-center"
						}
					>
						<BackButton icon={market.config.icons.backArrow} />
						<h2 className="category-title only-mobile">
							{dt(title)}
						</h2>
					</div>
					<div className="row searcher-row w-100">
						<div className="col-12 only-mobile d-none">
							<SearchFormTwo
								isInputClicked={isInputClicked}
								setIsInputClicked={setIsInputClicked}
							/>
						</div>
					</div>
				</div>
			)}
			<div style={{ marginBottom: "25px" }}>
				<div
					className="w-100 row p-3 m-auto filter-toolbox-block"
					// style={fixedFilter}
				>
					<div className="filter-b col-6 text-center align-self-center">
						<div
							className="d-flex text-center justify-content-center"
							onClick={() => toggleSideBar()}
							style={{ cursor: "pointer" }}
						>
							{market.config.home === 5 ? (
								<img
									src={market.config.icons.filter}
									alt="filter"
								/>
							) : (
								<svg
									id="icon-primary"
									width="15px"
									className="mr-2"
									viewBox="0 0 48 48"
									xmlns="http://www.w3.org/2000/svg"
								>
									<title />
									<path d="M47,12a2,2,0,0,0-2-2H24a2,2,0,0,0,0,4H45A2,2,0,0,0,47,12Z" />
									<path d="M3,14H8.35a6,6,0,1,0,0-4H3a2,2,0,0,0,0,4Zm11-4a2,2,0,1,1-2,2A2,2,0,0,1,14,10Z" />
									<path d="M45,22H37.65a6,6,0,1,0,0,4H45a2,2,0,0,0,0-4ZM32,26a2,2,0,1,1,2-2A2,2,0,0,1,32,26Z" />
									<path d="M22,22H3a2,2,0,0,0,0,4H22a2,2,0,0,0,0-4Z" />
									<path d="M45,34H28a2,2,0,0,0,0,4H45a2,2,0,0,0,0-4Z" />
									<path d="M18,30a6,6,0,0,0-5.65,4H3a2,2,0,0,0,0,4h9.35A6,6,0,1,0,18,30Zm0,8a2,2,0,1,1,2-2A2,2,0,0,1,18,38Z" />
								</svg>
							)}
							Фильтр
						</div>
					</div>
					<div className="sort-b col-6 text-center align-self-center">
						<ToolboxTwo
							setQuery={setQuery}
							sortIcon={market.config.icons.sort}
							cols={cols}
							pages={pages}
							setCols={setCols}
						/>
					</div>
				</div>
			</div>
			<div className="filter-canceller">
				<FilterCanceler query={query} setQuery={setQuery} />
			</div>
			{loading ? (
				<SimpleShimmerProducts columns={units[column] / 4} />
			) : (
				<div className="products mb-3">
					{pages.variations.length === 0 ? (
						<div className="row">
							<h4 className="ml-3">{t("nothing-found")}</h4>
						</div>
					) : (
						<Fragment>
							<InfiniteScroll
								dataLength={pages.variations.length}
								next={fetchMoreData}
								hasMore={pages.hasMore}
								loader={
									<div className="w-100">
										<SimpleShimmerProducts />
									</div>
								}
								className="row"
							>
								{pages.variations.map(product => (
									<div
										className={`${grid[cols]} d-flex`}
										key={product.variation.id}
									>
										<ProductCard
											product={product}
											type="sidebar"
											onAddToCart={dataProduct => {
												onAddToCart(dataProduct);
											}}
										/>
									</div>
								))}
							</InfiniteScroll>
							<LoadMoreButton
								pages={pages}
								fetchMoreData={fetchMoreData}
							/>
							{pages.allPages > 1 ? (
								<div className="page-pagination">
									<Pagination
										pageCount={pages.allPages}
										onChange={({ selected }) => {
											setQuery(prev => ({
												...prev,
												page: 1 + selected
											}));
										}}
										forcePage={query.page}
									/>
								</div>
							) : null}
							<QuickView />
							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
								addToCart={addToCart}
							/>
						</Fragment>
					)}
				</div>
			)}
			<SearchModal search={search} setSearch={setSearch} />
		</div>
	);
}

export default ProductListFour;
