import React from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { dt } from "../../../i18n";
import { getImageUrl } from "../../../utils/oxMethods";
import market from "../../../config/market";
import MediaImage from "../../../components/features/product/MediaImage";

export const MenuListTwo = ({ subMenu }) => {
	return (
		<ul
			className={`menu-list ${
				subMenu?.children?.length > 0 ? `active` : ``
			}`}
		>
			{subMenu?.children?.map((childMenu, i) => (
				<li key={i} className="col-6">
					<Link
						to={`/category/${childMenu.slug}`}
						className="child-menu-link"
					>
						{childMenu.name}
					</Link>
				</li>
			))}
		</ul>
	);
};

export const MenuChildListTwo = ({
	menuAllActive,
	setChildMenuChild,
	childMenuChild,
	setMenuCatalog,
	setChildMenu,
	menuChild,
	// seeMore = false,
	// slice = 11
}) => {
	return (
		<ul
			className={`child-menu-list row ${menuAllActive ? `active` : ``}`}
			onMouseLeave={() => setChildMenuChild(null)}
		>
			<div className="col-4">
				{menuChild?.map((subMenu) => (
					<li key={subMenu.id} className={`menu-item`}>
						<Link
							to={`/category/${subMenu.slug}`}
							onClick={() => {
								setMenuCatalog && setMenuCatalog(false);
								setChildMenu && setChildMenu(null);
							}}
							className="menu-link"
							onMouseEnter={() => setChildMenuChild(subMenu)}
						>
							<span
								className={`${subMenu.image ? `` : `no-image`}`}
							>
								<img
									src={getImageUrl(subMenu.image)}
									alt={subMenu.name}
								/>
								{dt(subMenu.name)}
							</span>
							{subMenu.children.length > 0 ? (
								<img
									src={market.config.icons.rArrow}
									alt="arrow"
								/>
							) : null}
						</Link>
					</li>
				))}
			</div>
			<div className="col-8">
				<MenuListTwo
					subMenuSlug={childMenuChild?.slug}
					subMenu={childMenuChild}
				/>
			</div>
		</ul>
	);
};

function HomeMenuTwo({ setMenuChild, setMenuAllActive }) {
	const { menuVertical } = useSelector((state) => state.menu);
	const { t } = useTranslation();

	return (
		<div className="menu-wrapper">
			<h3 className="menu-heading">{t("catalog_products")}</h3>
			<ul className="menu-list">
				{menuVertical.slice(0, 12).map((menu) => {
					return (
						<li
							key={menu.id}
							className="menu-item"
							onMouseEnter={() => {
								setMenuAllActive(false);
								setMenuChild(menu.children);
							}}
						>
							<Link
								className="menu-link"
								to={`${
									menu.children.length > 0
										? "#"
										: `/category/${menu.slug}`
								}`}
							>
								{menu.image ? (
									<MediaImage
										image={menu.image}
										largeSize="50x_"
										mediumSize="50x_"
										smallSize="50x_"
										alt={menu.name}
									/>
								) : null}
								<span>{dt(menu.name)}</span>
							</Link>
							{menu.children.length > 0 ? (
								<i className="icon-angle-right"></i>
							) : null}
						</li>
					);
				})}
				<li
					className="menu-item"
					onMouseEnter={() => {
						setMenuChild("");
					}}
				>
					<Link className="menu-link" to={`/all-categories`}>
						<img
							src="/assets/images/vibo/all_categories.svg"
							alt={t("all_categories")}
						/>
						<span>{t("all_categories")}</span>
					</Link>
					<i className="icon-angle-right"></i>
				</li>
			</ul>
		</div>
	);
}

export default HomeMenuTwo;
