import React from "react";
import market from "../../../config/market";
import MobileMenuOne from "./Mobile/MobileMenuOne";
import MobileMenuThree from "./Mobile/MobileMenuThree";
import MobileMenuTwo from "./Mobile/MobileMenuTwo";

export default function MobileMenu() {
	switch (market.config.mobileMenu) {
		case 1:
			return <MobileMenuOne home={market.config.home} />;
		case 2:
			return <MobileMenuTwo />;
		case 3:
			return <MobileMenuThree />;
		default:
			return <MobileMenuOne />;
	}
}
