import React, { useRef, useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import { connect, useSelector } from "react-redux";
import Breadcrumb from "../../../components/common/breadcrumb";
import FilterSide from "../filterSidebar/filters/FilterSide";
import ProductListOne from "../../../components/features/product/details/ProductListOne";
import { deepEqual } from "../../../utils/array";
import { useHistory } from "react-router-dom";
import market from "../../../config/market";
import { dt } from "../../../i18n";
import { getSingleCollection } from "../../../api/collections";
import HomeMenuTwo from "../../home/components/HomeMenuTwo";
// import { MenuChildList } from "../../home/components/HomeMenu";
import { MenuChildListTwo } from "../../home/components/MenuChildListTwo";

let timeOut = setTimeout(() => {}, 1000);
const KEEP_TIME = 1000;
const queryString = require("qs");
const initialQuery = {
	page: 1,
	size: market.subdomain === "gshop" ? 32 : 12,
};

function CategoryListOne(props) {
	const menu = props.menu;
	const history = useHistory();
	const { menuVertical } = useSelector((state) => state.menu);
	const [childMenu, setChildMenu] = useState(null);
	const [collection, setCollection] = useState();
	const [childMenuChild, setChildMenuChild] = useState(null);
	const [query, setQuery] = useState(
		window.location.search.length > 0
			? queryString.parse(window.location.search.slice(1))
			: { ...initialQuery }
	);

	if (!menu) {
		history.push(process.env.PUBLIC_URL + "/404");
	}
	const myRef = useRef(null);
	const executeScroll = () =>
		myRef.current.scrollIntoView({ behavior: "smooth" });

	useEffect(() => {
		if (window.location.search.length === 0) {
			if (!deepEqual(query, initialQuery)) {
				setQuery(initialQuery);
			}
		}
		getSingleCollection(menu.collection).then((res) => {
			setCollection(res);
		});
	}, [menu?.id]);
	useEffect(() => {
		if (
			(market.config.home === 8 || market.config.home === 6) &&
			window.innerWidth < 991
		) {
			document.querySelector(".container.search").classList.add("d-none");
		}
		return () => {
			const div = document.querySelector(".container.search");
			if (div) div.classList.remove("d-none");
		};
	}, []);
	return (
		<>
			<Helmet>
				<title>{collection?.title}</title>
				<meta name="description" content={collection?.description} />
				<meta name="keywords" content={collection?.keyword} />
			</Helmet>

			<h1 className="d-none">
				{dt(menu?.name)} | {dt(props.title)}
			</h1>

			<div className="main category-page" ref={myRef}>
				<Breadcrumb
					breadcrumb={props.menus.map((menu) => ({
						link: menu?.slug,
						name: menu?.name,
					}))}
				/>
				<div className="page-content">
					<div className="container">
						<div className="row align-items-start">
							<div className="col-lg-9 skeleton-body skel-shop-products loaded">
								{childMenu && market.config.home === 9 ? (
									<div
										className="menu-with-slider"
										onMouseLeave={() => setChildMenu(null)}
									>
										<div className="menu-slider row">
											<div className="banners-childmenus">
												<div className="background" />
												{/* <MenuChildList
													menuAllActive={[]}
													setMenuAllActive={() => {}}
													setMenuCatalog={() => {}}
													menuChild={childMenu}
													setChildMenu={setChildMenu}
													seeMore={true}
												/> */}
												<MenuChildListTwo
													menuAllActive={[]}
													setMenuAllActive={() => {}}
													setMenuCatalog={() => {}}
													menuChild={childMenu}
													setChildMenuChild={
														setChildMenuChild
													}
													childMenuChild={
														childMenuChild
													}
													seeMore={true}
												/>
											</div>
										</div>
									</div>
								) : null}
								<ProductListOne
									query={query}
									setQuery={setQuery}
									menu={menu}
									column="4cols"
									title={menu.name}
									executeScroll={executeScroll}
								/>

								{collection ? (
									<>
										<div
											className="footer-desc my-4"
											dangerouslySetInnerHTML={{
												__html: collection.footerDesc,
											}}
										/>
										<p className="footer-title text-dark">
											{collection.footerTitle}
										</p>
									</>
								) : null}
							</div>

							<div className="col-lg-3 order-lg-first skeleton-body skel-shop-sidebar loaded">
								{menuVertical.length &&
								market.config.home === 9 ? (
									<div className="menu-with-slider">
										<div className="menu-slider row">
											<div className="banners-childmenus">
												<div className="menus only-desktop">
													<HomeMenuTwo
														setChildMenu={
															setChildMenu
														}
													/>
												</div>
											</div>
										</div>
									</div>
								) : null}
								<FilterSide
									onSubmit={(values) => {
										clearTimeout(timeOut);
										timeOut = setTimeout(() => {
											if (!deepEqual(query, values)) {
												setQuery((prevQuery) => ({
													...prevQuery,
													...values,
													page: 1,
												}));
											}
										}, KEEP_TIME);
									}}
									clearAll={() => {
										setQuery(initialQuery);
									}}
									menu={menu}
									adClass="sidebar sidebar-shop"
									executeScroll={executeScroll}
									initialValues={query}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

const mapStateToProps = (state, props) => {
	const menu = state.menu.menuList.find(
		(menu) => menu.slug === props.match.params.slug
	);
	const menus = [menu];
	const menuList = state.menu.menuList;
	if (menus.length > 0) {
		for (let i = 0; ; i++) {
			if (menus[i]?.parent) {
				let parentMenu = menuList.find(
					(menu) => menu.id === menus[i].parent
				);
				menus.push(parentMenu);
			} else {
				break;
			}
		}
	}
	return {
		menu: state.menu.menuList.find(
			(menu) => menu.slug === props.match.params.slug
		),
		menus: menus.reverse(),
		props: props,
		title: state.marketData.title,
	};
};

export default React.memo(connect(mapStateToProps)(CategoryListOne));
