import React from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { dt } from "../../../i18n";
import MediaImage from "../../../components/features/product/MediaImage";
import market from "../../../config/market";

function HomeMenuTwo({ setChildMenu }) {
	const { menuVertical } = useSelector((state) => state.menu);
	const { t } = useTranslation();

	return (
		<div className="menu-wrapper">
			<h3 className="menu-heading">{t("catalog_products")}</h3>
			<ul className="menu-list">
				{menuVertical.map((menu) => (
					<li
						className="menu_item"
						key={menu.id}
						onMouseEnter={() => setChildMenu(menu.children)}
					>
						<Link
							to={`/category/${menu.slug}`}
							onClick={() => setChildMenu(null)}
						>
							<div className="picture">
								<MediaImage
									image={menu.image}
									largeSize="50x_"
									mediumSize="50x_"
									smallSize="50x_"
									alt={menu.originalName}
								/>
							</div>
							<span className="d-flex justify-content-between align-items-center w-100">
								{dt(menu.name)}
								{menu.children.length > 0 ? (
									<img
										src={market.config.icons.rArrow}
										alt="arrow"
									/>
								) : null}
							</span>
						</Link>
					</li>
				))}
			</ul>
		</div>
	);
}

export default HomeMenuTwo;
